import classNames from "classnames";
import React, { useEffect, useLayoutEffect, useRef, useState } from "react";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { Button } from "../Button/Button";
import { IconButton } from "../IconButton/IconButton";
import styles from "./Flyout.module.scss";
import { FlyoutSlot, FlyoutSlotProps } from "./FlyoutSlot";

const getFlyoutPosition = (triggerNode: HTMLElement | null, flyoutNode: HTMLElement | null) => {
  const boxShadowSpace = 3;

  if (!triggerNode || !flyoutNode) return { x: boxShadowSpace, y: boxShadowSpace };

  const { width, top, height, bottom, left, right } = triggerNode.getBoundingClientRect();
  const { width: flyoutWidth, height: flyoutHeight } = flyoutNode.getBoundingClientRect();

  const direction = {
    horizontal: "left",
    vertical: "bottom",
    overflowed: false,
  };

  if (left + width + 16 < flyoutWidth) {
    direction.horizontal = "right";
  }

  // if flyout overflows bottom of the screen
  if (top + height + 8 + flyoutHeight > window.innerHeight) {
    direction.vertical = "top";

    // if flyout overflows both bottom and top of the screen
    if (bottom - flyoutHeight < 0) {
      const flyoutStartPosition = Math.ceil(bottom + 8);
      const flyoutHeight = window.innerHeight - flyoutStartPosition;

      direction.vertical = "bottom";
      direction.overflowed = true;

      flyoutNode.style.overflowY = "auto";
      flyoutNode.style.height = `${flyoutHeight}px`;
    }
  }

  return {
    x:
      direction.horizontal === "left"
        ? right + 16 - flyoutWidth - boxShadowSpace
        : left - 16 + boxShadowSpace,
    y: direction.vertical === "bottom" ? bottom + 8 : top - flyoutHeight - 8,
    overflowed: direction.overflowed,
  };
};

export interface FlyoutProps {
  triggerRef: React.RefObject<HTMLElement>;
  dataTestId?: string;
  children?: React.ReactNode | React.ReactNode[];
  show?: boolean;
  onClose: () => void;
  size?: "medium" | "large" | number;
  title?: string | React.ReactNode;
  action?: {
    label: string;
    onClick: () => void;
  };
  className?: string;
}

export const Flyout: React.FC<FlyoutProps> & { Slot: React.FC<FlyoutSlotProps> } = ({
  dataTestId,
  children,
  show,
  onClose,
  size = "medium",
  title,
  action,
  triggerRef,
  className,
}) => {
  const hasMultipleChildren = Array.isArray(children);
  const flyoutContainerRef = useRef<HTMLDivElement>(null);
  const [position, setPosition] = useState<{ x: number; y: number } | null>(null);
  const [overflowed, setOverflowed] = useState<boolean | null>(null);
  const [scrollTop, setScrollTop] = useState<number | null>(null);

  useEffect(() => {
    if (!triggerRef.current) return;

    const handleScroll = () => {
      setScrollTop(window.document.documentElement.scrollTop);
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [triggerRef]);

  // close flyout on outside click
  useEffect(() => {
    const listener = (event: Event) => {
      const el = flyoutContainerRef?.current;
      const target = event.target as HTMLElement;
      if (!el || el.contains(target) || target === triggerRef.current) {
        return;
      }

      onClose();
    };

    document.addEventListener("mousedown", listener);

    return () => {
      document.removeEventListener("mousedown", listener);
    };
  }, [flyoutContainerRef, onClose, triggerRef]);

  useLayoutEffect(() => {
    if (!show) {
      return;
    }

    const pos = getFlyoutPosition(triggerRef.current, flyoutContainerRef.current);

    if (pos.overflowed && overflowed === null) {
      setOverflowed(true);

      flyoutContainerRef.current?.addEventListener("scroll", () => {
        setOverflowed(false);
      });
    }

    setPosition({ x: pos.x, y: pos.y });
  }, [flyoutContainerRef, triggerRef, show, overflowed, scrollTop]);

  useLayoutEffect(() => {
    const handleResize = (event: Event) => {
      const pos = getFlyoutPosition(triggerRef.current, flyoutContainerRef.current);
      setPosition({ x: pos.x, y: pos.y });
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, [triggerRef]);

  if (!show) {
    return null;
  }

  return (
    <div
      ref={flyoutContainerRef}
      data-testid={dataTestId}
      style={{
        top: position?.y,
        left: position?.x,
        width: size === "medium" ? 360 : size === "large" ? 420 : size,
      }}
      className={classNames(styles.wrapper, className, {
        [styles.large]: size === "large",
      })}
    >
      <div className={styles.header}>
        {title && <span className="l1-semibold">{title}</span>}
        {action && (
          <Button className={styles.action} onClick={action.onClick} variant="ghost" size="small">
            {action.label}
          </Button>
        )}
        {overflowed && (
          <IconButton
            className={styles.overflowArrow}
            iconName={Icons.ArrowDown}
            onClick={() => flyoutContainerRef.current?.scrollBy({ top: 10, behavior: "smooth" })}
            iconColor={Colors.SurfaceNeutralMuted}
          />
        )}
      </div>

      {hasMultipleChildren
        ? children.map((component, idx) => <React.Fragment key={idx}>{component}</React.Fragment>)
        : children}
    </div>
  );
};

Flyout.Slot = FlyoutSlot;
