import React from "react";

import { Icons } from "../../icon-types";
import { Button } from "../Button/Button";
import { Icon } from "../Icon/Icon";
import styles from "./Flyout.module.scss";

export interface FlyoutSlotProps {
  dataTestId?: string;
  children?: React.ReactNode;
  icon?: Icons;
  title?: string | React.ReactNode;
  action?: {
    label: string;
    onClick: () => void;
  };
}

export const FlyoutSlot: React.FC<FlyoutSlotProps> = ({
  dataTestId,
  children,
  icon,
  title,
  action,
}) => (
  <div data-testid={dataTestId} className={styles.content}>
    <div className={styles.header}>
      {icon && (
        <Icon dataTestId={`${dataTestId}-icon`} className={styles.icon} iconName={icon} size={16} />
      )}
      {title && <span className="l2-semibold">{title}</span>}
      {action && (
        <Button className={styles.action} onClick={action.onClick} variant="ghost" size="small">
          {action.label}
        </Button>
      )}
    </div>
    {children}
  </div>
);
