import * as chrono from "chrono-node";
import classNames from "classnames";
import {
  addDays,
  endOfDay,
  endOfMonth,
  format,
  isAfter,
  isBefore,
  isSameDay,
  isSameMonth,
  isSameYear,
  isToday,
  previousMonday,
  setMonth,
  startOfDay,
  startOfToday,
} from "date-fns";
import { differenceInDays } from "date-fns/differenceInDays";
import React, { useEffect, useRef, useState } from "react";

import { useCopy } from "../../../extensions";
import { NakedButton } from "../../../shared/components/NakedButton/NakedButton";
import { Locale } from "../../../utils/locale";
import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import { IconButton } from "../IconButton/IconButton";
import { Input } from "../Input/Input";
import { Tooltip } from "../Tooltip/Tooltip";
import styles from "./DatePicker.module.scss";
import { LOCALE_CONFIG_MAP } from "./localeConfig";

export interface DatePickerValue {
  from: Date | null;
  to: Date | null;
}

interface DateModifier {
  range: DatePickerValue;
  disabled?: boolean;
  wrapperClassName?: string;
  cellClassName?: string;
  tooltipText?: string;
}

export interface DatePickerProps {
  dataTestId?: string;
  locale?: Locale;
  defaultValue?: DatePickerValue;
  value?: DatePickerValue;
  onChange?: (value: DatePickerValue) => void;
  disableFutureDates?: boolean;
  /**
   * Display variant of the date picker
   * @default "integrated"
   * @note "floating" variant lacks styling for mobile devices
   */
  pickerVariant?: "integrated" | "floating";
  dateModifiers?: DateModifier[];
  className?: string;
  selectionLimits?: { minDays?: number; maxDays?: number };
}

export interface DatePickerConfig {
  parserFn: typeof chrono.parse;
  locale: Locale;
  weekdayNames: string[];
  monthNames: string[];
  placeholder: string;
  formatString: string;
}

type GridMode = "month" | "year";

const hedgeFromDate = (inputDate: Date | undefined, toDate: Date | undefined): Date => {
  let date = inputDate ?? new Date();
  date = startOfDay(date);
  if (date && toDate) {
    return isAfter(date, toDate) ? toDate : date;
  }
  return date;
};

const hedgeToDate = (
  inputDate: Date | undefined,
  fromDate: Date | undefined,
  disableFutureDates?: boolean
): Date => {
  let date =
    !inputDate || (disableFutureDates && isAfter(inputDate, new Date())) ? new Date() : inputDate;
  date = endOfDay(date);
  if (date && fromDate) {
    return isBefore(date, fromDate) ? fromDate : date;
  }
  return date;
};

const isSameOrBeforeDay = (date: Date, dateToCompare: Date) =>
  isSameDay(date, dateToCompare) || isBefore(date, dateToCompare);

const isDateInRange = (date: Date, fromDate: Date, toDate: Date) =>
  !isSameDay(fromDate, toDate) &&
  isSameOrBeforeDay(fromDate, date) &&
  isSameOrBeforeDay(date, toDate);

const sortDates = (firstDate: Date, secondDate: Date): Date[] =>
  [firstDate, secondDate].sort((a, b) => a.getTime() - b.getTime());

const hasDisabledDatesInRange = (
  firstDate: Date,
  secondDate: Date,
  dateModifiers: DateModifier[]
): boolean => {
  const disabledModifiers = dateModifiers.filter((modifier) => modifier.disabled);
  const [fromDate, toDate] = sortDates(firstDate, secondDate);

  return disabledModifiers.some((modifier) => {
    const modifierStart = modifier.range.from;
    const modifierEnd = modifier.range.to;

    if (!modifierStart || !modifierEnd) return false;

    // Check if selected range overlaps with modifier range
    return (
      isDateInRange(fromDate, modifierStart, modifierEnd) ||
      isDateInRange(toDate, modifierStart, modifierEnd) ||
      isDateInRange(modifierStart, fromDate, toDate)
    );
  });
};

const findFirstAvailableDate = (
  dateModifiers: DateModifier[],
  selectionLimitMinDays: number | undefined
) => {
  let firstAvailableDate = startOfToday();
  const maxDays = 1000;

  for (let i = 0; i < maxDays; i++) {
    firstAvailableDate = addDays(firstAvailableDate, 1);

    const hasDisabledDates = hasDisabledDatesInRange(
      firstAvailableDate,
      addDays(firstAvailableDate, selectionLimitMinDays ?? 0),
      dateModifiers
    );

    if (!hasDisabledDates) return firstAvailableDate;
  }

  return null;
};

// used to make sure a given value starts at 00:00:00 (from) and ends at 23:59:59 (to)
const normalizeValue = (value: DatePickerValue | undefined): DatePickerValue | undefined => {
  if (!value) return;
  return {
    from: value.from ? startOfDay(value.from) : null,
    to: value.to ? endOfDay(value.to) : null,
  };
};

export const DatePicker: React.FC<DatePickerProps> = ({
  dataTestId,
  locale,
  defaultValue,
  onChange,
  value,
  disableFutureDates,
  pickerVariant = "integrated",
  dateModifiers: preprocessedDateModifiers,
  className,
  selectionLimits,
}) => {
  if (value && defaultValue) {
    console.warn(
      "Use either value and onChange to control the component OR defaultValue and onChange when used as uncontrolled"
    );
  }

  const dateModifiers: DateModifier[] =
    preprocessedDateModifiers?.map(({ range, ...rest }): DateModifier => {
      const normalizedValue = normalizeValue(range);
      const [from, to] =
        normalizedValue?.from && normalizedValue?.to
          ? sortDates(normalizedValue.from, normalizedValue.to)
          : [null, null];
      return {
        ...rest,
        range: { from, to },
      };
    }) ?? [];

  const today = startOfToday();
  const config = locale ? LOCALE_CONFIG_MAP[locale] : LOCALE_CONFIG_MAP["en"];

  let initialValue = normalizeValue(defaultValue);

  if (
    initialValue?.from &&
    initialValue.to &&
    initialValue.to.getTime() < initialValue.from.getTime()
  ) {
    console.warn("Invalid date range. Check value/defaultValue.");
    initialValue = undefined;
  }
  const initialDate = findFirstAvailableDate(dateModifiers, selectionLimits?.minDays) || today;

  const { _startDate, _endDate } = useCopy("DatePicker");
  const [initialMonth, setInitialMonth] = useState(initialDate.getMonth());
  const [initialYear, setInitialYear] = useState(initialDate.getFullYear());
  const [gridMode, setGridMode] = useState<GridMode | null>(null);
  const [fromDate, setFromDate] = useState<Date | undefined>(initialValue?.from ?? undefined);
  const [fromDateInput, setFromDateInput] = useState<string>(
    initialValue?.from ? format(initialValue?.from, config.formatString) : ""
  );
  const [toDate, setToDate] = useState<Date | undefined>(initialValue?.to ?? undefined);
  const [toDateInput, setToDateInput] = useState<string>(
    initialValue?.to ? format(initialValue?.to, config.formatString) : ""
  );
  const [showPicker, setShowPicker] = useState(false);
  const fromInputRef = useRef<HTMLInputElement>(null);
  const toInputRef = useRef<HTMLInputElement>(null);
  const pickerRef = useRef<HTMLDivElement>(null);

  const baseDate = new Date(initialYear, initialMonth, 1);

  const normalizedValue = normalizeValue(value);

  useEffect(() => {
    if (!normalizedValue) return;

    if (
      normalizedValue.from &&
      normalizedValue.to &&
      normalizedValue.to.getTime() < normalizedValue.from.getTime()
    ) {
      console.warn("Invalid date range. Check normalizedValue prop.");
      return;
    }

    if (normalizedValue.from && normalizedValue.from.getTime() !== fromDate?.getTime()) {
      setFromDate(normalizedValue.from);
    }

    if (normalizedValue.to && normalizedValue.to.getTime() !== toDate?.getTime()) {
      setToDate(normalizedValue.to);
    }
  }, [normalizedValue, fromDate, toDate]);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const target = event.target as HTMLElement;

      if (
        !fromInputRef.current?.contains(target) &&
        !toInputRef.current?.contains(target) &&
        !pickerRef.current?.contains(target) &&
        !target.closest("[data-cell-click]")
      ) {
        setShowPicker(false);
        fromInputRef.current?.blur();
        toInputRef.current?.blur();
      }
    };

    document.body.addEventListener("click", handleClickOutside);

    return () => {
      document.body.removeEventListener("click", handleClickOutside);
    };
  }, []);

  const handleBlur = (event: React.FocusEvent<HTMLInputElement>, isFromDate: boolean) => {
    const input = event.target?.value;
    if (!input) return;

    const parsingResults = config.parserFn(input);

    if (isFromDate) {
      const validDate = hedgeFromDate(parsingResults[0]?.date(), toDate);
      if (toDate && checkRangeForDisabledDates(validDate, toDate)) {
        setFromDate(validDate);
        return;
      }
      setFromDate(validDate);
      setFromDateInput(format(validDate, config.formatString));
      if (validDate.getTime() === fromDate?.getTime()) return;
      onChange?.({ from: validDate, to: toDate ?? null });
    } else {
      const validDate = hedgeToDate(parsingResults[0]?.date(), fromDate, disableFutureDates);
      if (fromDate && checkRangeForDisabledDates(fromDate, validDate)) {
        setFromDate(validDate);
        return;
      }
      setToDate(validDate);
      setToDateInput(format(validDate, config.formatString));
      if (validDate.getTime() === toDate?.getTime()) return;
      onChange?.({ from: fromDate ?? null, to: validDate });
    }
  };

  const checkRangeForDisabledDates = (fromDate: Date, toDate: Date) => {
    const cleanSelection = () => {
      setFromDate(undefined);
      setFromDateInput("");
      setToDate(undefined);
      setToDateInput("");
    };

    if (
      hasDisabledDatesInRange(fromDate, toDate, dateModifiers) ||
      (selectionLimits?.minDays && differenceInDays(toDate, fromDate) < selectionLimits.minDays) ||
      (selectionLimits?.maxDays && differenceInDays(toDate, fromDate) > selectionLimits?.maxDays)
    ) {
      cleanSelection();
      return true;
    }
    return false;
  };

  const handleDateSelection = (date: Date) => {
    if (!fromDate) {
      const validDate = hedgeFromDate(date, toDate);

      if (toDate && checkRangeForDisabledDates(validDate, toDate)) {
        setFromDate(validDate);
        return;
      }

      setFromDate(validDate);
      onChange?.({ from: validDate, to: toDate ?? null });
      return;
    }

    if (!toDate) {
      if (checkRangeForDisabledDates(fromDate, date)) {
        setFromDate(date);
        return;
      }

      if (isBefore(date, fromDate)) {
        setFromDate(date);
        setToDate(undefined);
        setToDateInput("");
        onChange?.({ from: date ?? null, to: null });
        return;
      }
      const validDate = hedgeToDate(date, fromDate, disableFutureDates);
      setToDate(validDate);
      onChange?.({ from: fromDate ?? null, to: validDate });
      return;
    }

    if (fromDate && toDate) {
      if (isSameDay(date, toDate) || isSameDay(date, fromDate)) {
        // reset after re-click the fromDate or toDate
        setFromDate(date);
        setToDate(undefined);
        setToDateInput("");
        onChange?.({ from: date ?? null, to: null });
        return;
      }
      if (isAfter(date, fromDate)) {
        // modify range (extend toDate)
        const exactDate = endOfDay(date);

        if (checkRangeForDisabledDates(fromDate, exactDate)) {
          setFromDate(exactDate);
          return;
        }

        setToDate(exactDate);
        onChange?.({ from: fromDate ?? null, to: exactDate });
        return;
      }

      if (checkRangeForDisabledDates(date, toDate)) {
        setFromDate(date);
        return;
      }

      // modify range (extend fromDate)
      setFromDate(date);
      onChange?.({ from: date, to: toDate });
    }
  };

  const isFloating = pickerVariant === "floating";

  const handlePreviousMonthClick = () => {
    const withChangeYear = initialMonth === 1;
    setInitialMonth(withChangeYear ? 12 : initialMonth - 1);
    if (withChangeYear) {
      setInitialYear((prevState) => prevState - 1);
    }
  };

  const handleNextMonthClick = () => {
    const withChangeYear = initialMonth === 12;
    setInitialMonth(withChangeYear ? 1 : initialMonth + 1);
    if (withChangeYear) {
      setInitialYear((prevState) => prevState + 1);
    }
  };

  return (
    <div className={styles.container}>
      <div
        data-testid={dataTestId}
        className={classNames(styles.datePicker, className, {
          [styles.datePickerFloatingVariant]: isFloating,
        })}
      >
        <div
          data-testid={`${dataTestId}-picker`}
          className={classNames({
            [styles.datePicker]: isFloating,
            [styles.floating]: isFloating,
            [styles.hidden]: !showPicker,
          })}
          ref={pickerRef}
        >
          <div className={styles.header}>
            <IconButton
              dataTestId="previous-month-button"
              iconName={Icons.AngleLeft}
              className={styles.changeMonthButton}
              onClick={handlePreviousMonthClick}
              iconSize={20}
            />
            <SelectButton
              label={config.monthNames[baseDate.getMonth()]}
              onClick={() => {
                setGridMode((prevState) => (prevState !== "month" ? "month" : null));
              }}
              active={gridMode === "month"}
            />
            <SelectButton
              label={baseDate.getFullYear().toString()}
              onClick={() => {
                setGridMode((prevState) => (prevState !== "year" ? "year" : null));
              }}
              active={gridMode === "year"}
            />
            <IconButton
              dataTestId="next-month-button"
              iconName={Icons.AngleRight}
              className={styles.changeMonthButton}
              onClick={handleNextMonthClick}
              iconSize={20}
            />
          </div>
          {(gridMode === "month" && (
            <MonthGrid
              baseDate={baseDate}
              onSelectMonth={(month) => {
                setInitialMonth(month);
                setGridMode(null);
              }}
              config={config}
              disableFutureDates={disableFutureDates}
            />
          )) ||
            (gridMode === "year" && (
              <YearGrid
                baseDate={baseDate}
                onSelectYear={(year) => {
                  setInitialYear(year);
                  setGridMode(null);
                }}
              />
            )) || (
              <DateGrid
                baseDate={baseDate}
                disableFutureDates={disableFutureDates}
                onSelectDate={handleDateSelection}
                fromDate={fromDate}
                toDate={toDate}
                config={config}
                dateModifiers={dateModifiers}
              />
            )}
        </div>
        <div className={classNames(styles.footer, { [styles.footerFloatingVariant]: isFloating })}>
          <Input
            style={{ width: isFloating ? 160 : 116 }}
            required
            label={isFloating ? _startDate : undefined}
            icon={isFloating ? Icons.Calendar : undefined}
            ariaLabel="from"
            dataTestId="datepicker-input-from"
            ref={fromInputRef}
            placeholder={config.placeholder}
            value={
              document.activeElement !== fromInputRef.current && fromDate
                ? format(fromDate, config.formatString)
                : fromDateInput
            }
            onChange={(event) => {
              setFromDateInput(event.target.value);
            }}
            onKeyUp={(event) => {
              if (event.key === "Enter") {
                toInputRef.current?.focus();
              }
            }}
            onFocus={() => setShowPicker(true)}
            onBlur={(e) => handleBlur(e, true)}
          />
          <Icon
            iconName={Icons.ArrowRight}
            color={Colors.ContentPrimaryMuted}
            size={isFloating ? 24 : 16}
            className={classNames({ [styles.arrowFloatingVariant]: isFloating })}
          />
          <Input
            style={{ width: isFloating ? 160 : 116 }}
            required
            label={isFloating ? _endDate : undefined}
            icon={isFloating ? Icons.Calendar : undefined}
            ariaLabel="to"
            dataTestId="datepicker-input-to"
            ref={toInputRef}
            placeholder={config.placeholder}
            value={
              document.activeElement !== toInputRef.current && toDate
                ? format(toDate, config.formatString)
                : toDateInput
            }
            onChange={(event) => {
              setToDateInput(event.target.value);
            }}
            onKeyUp={(event) => {
              if (event.key === "Enter") {
                toInputRef.current?.blur();
              }
            }}
            onFocus={() => setShowPicker(true)}
            onBlur={(e) => handleBlur(e, false)}
          />
        </div>
      </div>
    </div>
  );
};

interface DateGridProps {
  baseDate: Date;
  onSelectDate: (date: Date) => void;
  fromDate?: Date;
  toDate?: Date;
  config: DatePickerConfig;
  disableFutureDates?: boolean;
  dateModifiers?: DateModifier[];
}
function DateGrid({
  baseDate,
  onSelectDate,
  fromDate,
  toDate,
  config,
  disableFutureDates,
  dateModifiers,
}: DateGridProps) {
  const prevMonday = baseDate.getDay() === 1 ? baseDate : previousMonday(baseDate);

  return (
    <div className={styles.dateGrid}>
      {config.weekdayNames.map((weekday) => (
        <div className={styles.weekday} key={weekday}>
          {weekday}
        </div>
      ))}
      {Array(35)
        .fill(0)
        .map((_, index) => {
          const day = addDays(prevMonday, index);
          const inRange = fromDate && toDate && isDateInRange(day, fromDate, toDate);

          const dateModifier =
            dateModifiers?.find(
              (currentDateModifier) =>
                currentDateModifier.range.from &&
                currentDateModifier.range.to &&
                isDateInRange(day, currentDateModifier.range.from, currentDateModifier.range.to)
            ) ?? null;

          const isDataModifierStart =
            dateModifier?.range.from && isSameDay(dateModifier.range.from, day);

          const isDataModifierEnd = dateModifier?.range.to && isSameDay(dateModifier.range.to, day);

          const isSelected =
            (fromDate && isSameDay(day, fromDate)) || (toDate && isSameDay(day, toDate));

          const dateModifierId =
            dateModifier?.range.from && dateModifier.range.to
              ? `date-modifier-${format(dateModifier.range.from, "yyyy-MM-dd")}-${format(
                  dateModifier.range.to,
                  "yyyy-MM-dd"
                )}`
              : null;

          const cellId = `cell-${format(day, "yyyy-MM-dd")}`;

          const isFutureDateDisabled = disableFutureDates ? isAfter(day, new Date()) : false;
          const isDateModifierDisabled = dateModifier?.disabled;

          return (
            <div
              key={day.toISOString()}
              className={classNames(styles.cellWrapper, {
                [styles.rangeBoundary]: inRange,
                [styles.rangeStart]: fromDate && isSameDay(day, fromDate),
                [styles.rangeEnd]: toDate && isSameDay(day, toDate),
                [dateModifier?.wrapperClassName ?? ""]: Boolean(dateModifier),
                [styles.dataModifierStart]: isDataModifierStart,
                [styles.dataModifierEnd]: isDataModifierEnd,
              })}
            >
              <NakedButton
                data-testid={cellId}
                data-tip
                data-for={dateModifierId}
                className={classNames(styles.interactiveCell, {
                  [styles.muted]: !isSameMonth(day, baseDate),
                  [styles.disabled]: isFutureDateDisabled,
                  [styles.disabledDateModifier]: isDateModifierDisabled,
                  [styles.today]: isToday(day),
                  [styles.selected]: isSelected,
                  [styles.range]: inRange,
                  [dateModifier?.cellClassName ?? ""]: Boolean(dateModifier),
                })}
                onClick={() => {
                  if (isDateModifierDisabled) return;
                  onSelectDate(day);
                }}
              >
                {day.getDate()}
              </NakedButton>
              {dateModifierId && dateModifier?.tooltipText && (
                <Tooltip dataTestId={`tooltip-${dateModifierId}`} tooltipId={dateModifierId}>
                  {dateModifier.tooltipText}
                </Tooltip>
              )}
            </div>
          );
        })}
    </div>
  );
}

interface MonthGridProps {
  baseDate: Date;
  onSelectMonth: (month: number) => void;
  config: DatePickerConfig;
  disableFutureDates?: boolean;
}

function MonthGrid({ baseDate, onSelectMonth, config, disableFutureDates }: MonthGridProps) {
  return (
    <div className={styles.monthYearGrid}>
      {config.monthNames.map((month, index) => (
        <NakedButton
          className={classNames(styles.interactiveCell, {
            [styles.selected]: isSameMonth(setMonth(new Date(baseDate), index), baseDate),
            [styles.disabled]:
              disableFutureDates && isAfter(setMonth(new Date(), index), endOfMonth(new Date())),
          })}
          key={index}
          onClick={() => onSelectMonth(index)}
          data-cell-click
        >
          {month}
        </NakedButton>
      ))}
    </div>
  );
}

interface YearGridProps {
  baseDate: Date;
  onSelectYear: (year: number) => void;
}

function YearGrid({ baseDate, onSelectYear }: YearGridProps) {
  const ref = useRef<HTMLDivElement>(null);
  const currentYear = new Date().getFullYear();

  useEffect(() => {
    if (!ref.current) return;
    ref.current?.scrollTo(0, ref.current.scrollHeight);
  }, []);

  return (
    <div ref={ref} className={classNames(styles.monthYearGrid, styles.yearGrid)}>
      {Array(24)
        .fill(0)
        .map((_, index) => {
          const year = currentYear - 23 + index;
          return (
            <NakedButton
              className={classNames(styles.interactiveCell, {
                [styles.selected]: isSameYear(new Date(year, 0, 1), baseDate),
              })}
              key={index}
              onClick={() => onSelectYear(year)}
              data-cell-click
            >
              {year}
            </NakedButton>
          );
        })}
    </div>
  );
}

interface SelectButtonProps {
  label: string;
  active?: boolean;
  onClick: (event: React.MouseEvent) => void;
}

function SelectButton({ label, active, onClick }: SelectButtonProps) {
  return (
    <NakedButton
      className={classNames(styles.selectButton, {
        [styles.active]: active,
      })}
      onClick={onClick}
    >
      <span className={classNames(styles.label)}>{label}</span>
      <Icon className={styles.arrowIcon} iconName={Icons.ArrowFilledDown} size={16} />
    </NakedButton>
  );
}
