import classNames from "classnames";
import React from "react";
import ReactTooltip, { TooltipProps as ReactTooltipProps } from "react-tooltip";

import { Colors } from "../../constants";
import styles from "./Tooltip.module.scss";

export interface TooltipProps extends ReactTooltipProps {
  tooltipId: string;
  dataTestId?: string;
  withoutArrow?: boolean;
}

export const Tooltip: React.FC<TooltipProps> = ({
  tooltipId,
  withoutArrow,
  children,
  ...props
}) => (
  <ReactTooltip
    id={tooltipId}
    effect="solid"
    textColor={Colors.SurfaceInfoContrast}
    backgroundColor={Colors.SurfaceInfoEmphasized}
    className={classNames(styles.tooltipContainer, {
      [styles.noArrowDisplay]: withoutArrow,
    })}
    {...props}
  >
    {children}
  </ReactTooltip>
);
