import classNames from "classnames";
import React from "react";

import { Colors } from "../../constants";
import { useMediaQuerySmallerThan } from "../../hooks";
import { Icons } from "../../icon-types";
import { Button, ButtonProps } from "../Button/Button";
import { Icon } from "../Icon/Icon";
import { IconButton } from "../IconButton/IconButton";
import styles from "./Notification.module.scss";

export type NotificationType = "positive" | "negative" | "neutral" | "warning";

export type ToastType = "info" | "error";

type BaseProps = {
  dataTestId?: string;
  content: string | React.ReactElement;
  cta?: {
    action: () => void;
    title: string;
    alignBottom?: boolean;
    noWrap?: boolean;
    additionalButtonProps?: Omit<ButtonProps, "dataTestId" | "className" | "variant" | "onClick">;
  };
  noIcon?: boolean;
  className?: string;
  style?: React.CSSProperties;
};

export interface ToastNotificationProps extends BaseProps {
  variant?: "toast";
  type: ToastType;
}

export interface DefaultNotificationProps extends BaseProps {
  variant?: "default";
  type: NotificationType;
  onClose?: () => void;
}

export type NotificationProps = ToastNotificationProps | DefaultNotificationProps;

export const Notification: React.FC<NotificationProps> = ({
  dataTestId,
  type,
  variant = "default",
  content,
  cta,
  noIcon,
  className,
  style,
  ...restProps
}) => {
  const isSmallerThanXS = useMediaQuerySmallerThan("XS");
  const { onClose } = restProps as DefaultNotificationProps;
  const isCtaBtnAlignedBottom = (cta?.alignBottom || isSmallerThanXS) && variant !== "toast";

  const ctaBtn = (
    <Button
      dataTestId={dataTestId && `${dataTestId}-cta-btn`}
      className={classNames(styles.ctaBtn, {
        [styles.noWrap]: cta?.noWrap,
      })}
      variant="contrast"
      onClick={cta?.action}
      {...(cta?.additionalButtonProps ?? {})}
    >
      {cta?.title}
    </Button>
  );

  return (
    <div
      data-testid={dataTestId}
      className={classNames(styles.notification, styles[type], className)}
      style={style}
      data-type={type}
    >
      <div className={styles.row}>
        <div className={classNames(styles.iconAndContent, styles[type])}>
          {noIcon ? null : (
            <Icon
              dataTestId={dataTestId && `${dataTestId}-icon`}
              className={styles.icon}
              {...getIconPropsByType(type)}
            />
          )}
          <div>{content}</div>
        </div>
        {((cta && !isCtaBtnAlignedBottom) || onClose) && (
          <div
            className={classNames(styles.ctaAndCloseBtns, {
              [styles.isCtaBtnAlignedBottom]: isCtaBtnAlignedBottom,
            })}
          >
            {cta && !isCtaBtnAlignedBottom && ctaBtn}
            {onClose && (
              <IconButton
                dataTestId={dataTestId && `${dataTestId}-close-btn`}
                className={styles.closeBtn}
                iconName={Icons.Cross}
                size="large"
                onClick={onClose}
              />
            )}
          </div>
        )}
      </div>
      {cta && isCtaBtnAlignedBottom && ctaBtn}
    </div>
  );
};

const getIconPropsByType = (type: NotificationType | ToastType) => {
  switch (type) {
    case "info":
      return { iconName: Icons.InfoFilledBlack };
    case "error":
      return { iconName: Icons.AlertFilled, color: Colors.ContentPrimaryContrast };
    case "positive":
      return { iconName: Icons.TickCircleFilled, color: Colors.ContentAccentGreen };
    case "negative":
      return { iconName: Icons.AlertFilled, color: Colors.ContentErrorEmphasized };
    case "warning":
      return { iconName: Icons.InfoFilled, color: Colors.ContentAccentOrange };
    default:
      return { iconName: Icons.InfoFilled, color: Colors.ContentAccentPeriwinkle };
  }
};
