import classNames from "classnames";
import debounce from "just-debounce-it";
import React, { useContext, useEffect, useState } from "react";

import { CopyContext } from "../../../extensions";
import { NakedButton } from "../../../shared/components/NakedButton/NakedButton";
import { Colors } from "../../constants";
import { useMediaQuerySmallerThan } from "../../hooks";
import { Icons } from "../../icon-types";
import { Dropdown } from "../Dropdown/Dropdown";
import { Icon } from "../Icon/Icon";
import { IconButton } from "../IconButton/IconButton";
import styles from "./Pagination.module.scss";

const ellipsis = "...";

const debounceUpdate = debounce((fn: () => void) => fn(), 300);

export interface PaginationProps {
  dataTestId?: string;
  pageNumber: number;
  pageSize: number;
  pageCount: number;
  itemsTotal: number;
  itemsTotalLabel: string;
  onPageChange: (page: number) => void;
  onPageSizeSelect: (value: number) => void;
}

export const Pagination: React.FC<PaginationProps> = ({
  dataTestId,
  pageNumber,
  pageCount,
  onPageChange,
  itemsTotal,
  pageSize,
  onPageSizeSelect,
  itemsTotalLabel,
}) => {
  const [selectedPageNumber, setSelectedPageNumber] = useState(pageNumber);
  const isSmallerThanS = useMediaQuerySmallerThan("S");
  const { PaginationInfo } = useContext(CopyContext);

  const handlePageChange = (page: number | string) => {
    if (selectedPageNumber === page || typeof page === "string") return;

    setSelectedPageNumber(page);
    debounceUpdate(() => onPageChange(page));
  };

  useEffect(() => {
    setSelectedPageNumber(pageNumber);
  }, [pageNumber]);

  if (isSmallerThanS)
    return (
      <div className={styles.mobilePagination}>
        <div className={styles.iconButtons}>
          <IconButton
            className={classNames({ [styles.hide]: selectedPageNumber < 3 })}
            iconName={Icons.AngleDoubleLeft}
            onClick={() => handlePageChange(1)}
          />
          <IconButton
            className={classNames({ [styles.hide]: selectedPageNumber === 1 })}
            iconName={Icons.AngleLeft}
            onClick={() => handlePageChange(selectedPageNumber - 1)}
          />
        </div>
        <div className={styles.infoBlock}>
          <div className={styles.currentPage}>
            {PaginationInfo._capitalizedTextPage} <span>{selectedPageNumber}</span> of {pageCount}
          </div>
          <div className={styles.itemsTotal}>
            {itemsTotal} {itemsTotalLabel}
          </div>
        </div>
        <div className={styles.iconButtons}>
          <IconButton
            className={classNames({ [styles.hide]: selectedPageNumber === pageCount })}
            iconName={Icons.AngleRight}
            onClick={() => handlePageChange(selectedPageNumber + 1)}
          />
          <IconButton
            className={classNames({ [styles.hide]: selectedPageNumber > pageCount - 2 })}
            iconName={Icons.AngleDoubleRight}
            onClick={() => handlePageChange(pageCount)}
          />
        </div>
      </div>
    );

  return (
    <div className={styles.desktopPaginationWrapper}>
      <div className={styles.pageSizeSelector}>
        <span className={styles.showLabel}>{PaginationInfo._textShow}: </span>
        <Dropdown
          dataTestId={dataTestId && `${dataTestId}-page-size-select`}
          size="small"
          labelColor={Colors.ContentPrimaryEmphasized}
          items={[
            { label: "10", value: 10 },
            { label: "20", value: 20 },
            { label: "50", value: 50 },
          ]}
          defaultValue={pageSize}
          onSelect={(value) => onPageSizeSelect(value)}
        />
        <span className={styles.itemsTotalLabel}>
          {itemsTotal} {itemsTotalLabel}
        </span>
      </div>
      {pageCount > 1 && (
        <div
          data-testid={dataTestId}
          className={styles.desktopPagination}
          role="navigation"
          aria-label="Pagination"
        >
          {selectedPageNumber > 1 && (
            <NakedButton
              data-testid={dataTestId && `${dataTestId}-left-arrow`}
              className={styles.arrowIcon}
              role="button"
              aria-label="Previous page"
              onClick={() => handlePageChange(selectedPageNumber - 1)}
              onKeyDown={(e) => e.key === "Enter" && handlePageChange(selectedPageNumber - 1)}
            >
              <Icon iconName={Icons.AngleLeft} />
            </NakedButton>
          )}
          {renderPageNumbers(pageCount, selectedPageNumber).map((page, index) => (
            <div
              data-testid={dataTestId && `${dataTestId}-page-${page}`}
              key={index}
              className={classNames(styles.pageNumber, {
                [styles.current]: page === selectedPageNumber,
                [styles.asEllipsis]: page === ellipsis,
              })}
              role="button"
              tabIndex={page === ellipsis ? -1 : 0}
              aria-current={page === selectedPageNumber ? "page" : undefined}
              aria-label={`Page ${page}`}
              onClick={() => handlePageChange(page)}
              onKeyDown={(e) => e.key === "Enter" && handlePageChange(page)}
            >
              <span>{page}</span>
            </div>
          ))}
          {selectedPageNumber < pageCount && (
            <NakedButton
              data-testid={dataTestId && `${dataTestId}-right-arrow`}
              className={styles.arrowIcon}
              role="button"
              aria-label="Next page"
              onClick={() => handlePageChange(selectedPageNumber + 1)}
              onKeyDown={(e) => e.key === "Enter" && handlePageChange(selectedPageNumber + 1)}
            >
              <Icon iconName={Icons.AngleRight} />
            </NakedButton>
          )}
        </div>
      )}
    </div>
  );
};

const renderPageNumbers = (pageCount: number, selectedPageNumber: number) => {
  const pageNumbers = [...Array(pageCount).keys()].map((v) => v + 1);

  if (pageCount < 6) return pageNumbers;

  if (pageCount === 6) {
    if ([1, 2].includes(selectedPageNumber)) return [1, 2, 3, ellipsis, 6];
    if ([3, 4].includes(selectedPageNumber)) return [1, 2, 3, 4, 5, 6];
    if ([5, 6].includes(selectedPageNumber)) return [1, ellipsis, 4, 5, 6];
  }

  if (pageCount === 7) {
    if ([1, 2].includes(selectedPageNumber)) return [1, 2, 3, ellipsis, 7];
    if (selectedPageNumber === 3) return [1, 2, 3, 4, ellipsis, 7];
    if (selectedPageNumber === 4) return [1, 2, 3, 4, 5, 6, 7];
    if (selectedPageNumber === 5) return [1, ellipsis, 4, 5, 6, 7];
    if ([6, 7].includes(selectedPageNumber)) return [1, ellipsis, 5, 6, 7];
  }

  if (pageCount === 8) {
    if ([1, 2].includes(selectedPageNumber)) return [1, 2, 3, ellipsis, 8];
    if (selectedPageNumber === 3) return [1, 2, 3, 4, ellipsis, 8];
    if (selectedPageNumber === 4) return [1, 2, 3, 4, 5, ellipsis, 8];
    if (selectedPageNumber === 5) return [1, ellipsis, 4, 5, 6, 7, 8];
    if (selectedPageNumber === 6) return [1, ellipsis, 5, 6, 7, 8];
    if ([7, 8].includes(selectedPageNumber)) return [1, ellipsis, 6, 7, 8];
  }

  if (pageCount === 9) {
    if ([1, 2].includes(selectedPageNumber)) return [1, 2, 3, ellipsis, 9];
    if (selectedPageNumber === 3) return [1, 2, 3, 4, ellipsis, 9];
    if (selectedPageNumber === 4) return [1, 2, 3, 4, 5, ellipsis, 9];
    if (selectedPageNumber === 5) return [1, ellipsis, 4, 5, 6, ellipsis, 9];
    if (selectedPageNumber === 6) return [1, ellipsis, 5, 6, 7, 8, 9];
    if (selectedPageNumber === 7) return [1, ellipsis, 6, 7, 8, 9];
    if ([8, 9].includes(selectedPageNumber)) return [1, ellipsis, 7, 8, 9];
  }

  // pagesTotal > 9
  if ([1, 2].includes(selectedPageNumber)) return [1, 2, 3, ellipsis, pageCount];
  if (selectedPageNumber === 3) return [1, 2, 3, 4, ellipsis, pageCount];
  if (selectedPageNumber === 4) return [1, 2, 3, 4, 5, ellipsis, pageCount];
  if (selectedPageNumber > 4 && selectedPageNumber < pageCount - 3)
    return [
      1,
      ellipsis,
      selectedPageNumber - 1,
      selectedPageNumber,
      selectedPageNumber + 1,
      ellipsis,
      pageCount,
    ];

  if (selectedPageNumber === pageCount - 3) return [1, ellipsis, ...pageNumbers.slice(-5)];
  if (selectedPageNumber === pageCount - 2) return [1, ellipsis, ...pageNumbers.slice(-4)];

  return [1, ellipsis, ...pageNumbers.slice(-3)];
};
