import classNames from "classnames";
import React, { ChangeEvent, CSSProperties, useRef } from "react";

import { useCopy } from "../../../extensions";
import styles from "./Slider.module.scss";

interface CommonProps {
  dataTestId?: string;
  scaleMarkers?: number;
  withScaleLegend?: boolean;
  min?: number;
  max?: number;
  step?: number;
  tooltip?: "top" | "bottom";
  className?: string;
  width?: number | string;
}

export interface SliderProps extends CommonProps {
  range?: never;
  onChange: (value: number) => void;
  value: number;
  withInput?: boolean;
  percentageInput?: boolean;
  pushable?: never;
}

export interface RangeSliderProps extends CommonProps {
  range: true;
  onChange: (value: { min: number; max: number }) => void;
  value: { min: number; max: number };
  withInput?: never;
  percentageInput?: never;
  pushable?: boolean;
}

const getDecimalPart = (num: number) => {
  if (Number.isInteger(num)) {
    return 0;
  }

  const decimalStr = num.toString().split(".")[1];
  return Number(decimalStr);
};

export const Slider: React.FC<SliderProps | RangeSliderProps> = ({
  dataTestId,
  value,
  onChange,
  min = 0,
  max = 100,
  step = 1,
  scaleMarkers,
  withScaleLegend,
  withInput,
  percentageInput,
  tooltip,
  className,
  width,
  // range related
  range,
  pushable,
}) => {
  const scaleLegendStep = scaleMarkers ? (max - min) / (scaleMarkers - 1) : 1;
  const scaleLegendStepsArray = Array.from({ length: scaleMarkers ?? 1 }, (_, idx) =>
    idx === 0 ? min : Math.round(idx * scaleLegendStep) + min
  );

  const inputRef = useRef<HTMLInputElement>(null);

  const decimalPart = getDecimalPart(step);
  const decimalCount = decimalPart === 0 ? 0 : decimalPart.toString().length;

  const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
    const val = parseFloat(e.target.value);
    const newValue = max && val > max ? max : val;

    if (range) {
      const minValue = e.target.id === "min" ? newValue : value.min;
      const maxValue = e.target.id === "max" ? newValue : value.max;

      if (!pushable) {
        onChange({
          min:
            e.target.id === "min" ? (newValue < maxValue ? newValue : maxValue - step) : minValue,
          max:
            e.target.id === "max" ? (newValue > minValue ? newValue : minValue + step) : maxValue,
        });

        return;
      }

      if (minValue + step >= maxValue || maxValue - step <= minValue) {
        const clampMinMax = (val: number) => Math.min(Math.max(val, min), max);

        // ensures that values are in fact a range instead of two the same numbers.
        // f.e. min: 99, max: 100 instead of min: 100, max: 100
        const respectRange = (type: "min" | "max") => {
          if (type === "min") {
            return newValue + step >= max ? max - step : newValue;
          }

          return newValue - step <= min ? min + step : newValue;
        };

        onChange({
          min:
            e.target.id === "min"
              ? respectRange("min")
              : newValue - step <= minValue
              ? clampMinMax(newValue - step)
              : minValue,
          max:
            e.target.id === "max"
              ? respectRange("max")
              : newValue + step >= maxValue
              ? clampMinMax(newValue + step)
              : maxValue,
        });
      } else {
        onChange({
          min: e.target.id === "min" ? newValue : minValue,
          max: e.target.id === "max" ? newValue : maxValue,
        });
      }

      return;
    }

    e.target.value = newValue.toString();

    if (inputRef.current) {
      inputRef.current.value = newValue.toFixed(decimalCount === 0 ? 0 : decimalCount);
    }

    if (min && newValue < min) {
      return;
    }

    onChange(newValue);
  };

  const handleBlur = (e: React.ChangeEvent<HTMLInputElement>) => {
    // it is not supported for range
    if (range) return;

    const floatValue = parseFloat(e.target.value);
    let newValue;

    if (!floatValue || (min && floatValue < min)) {
      e.target.value = min.toFixed();
      newValue = min;
    } else if (max && floatValue > max) {
      e.target.value = max.toFixed();
      newValue = max;
    } else {
      newValue = floatValue;
    }

    e.target.value = newValue.toFixed(decimalCount === 0 ? 0 : decimalCount);
    onChange(newValue);
  };

  const calculateThumbPosition = (val: number) => {
    // get multiplier so calculations will always result in a number between 0% and 100%
    // f.e. if max is 200 and our value is 150, then instead of returning 150% it will return 75%
    // or if max is 200 and min is 100, and our value is 150, then it will return 50%
    const multiplier = 100 / (max - min);
    // don't ask me about this `0.18` magic number, I've took it from https://css-tricks.com/value-bubbles-for-range-inputs/
    const boundaryOffset = 4 - ((val - min) / (max - min)) * 100 * 0.18;

    return `calc(${(val - min) * multiplier}% + (${boundaryOffset}px))`;
  };

  const calculateOutputBottomOffset = () => {
    const isFirefox = navigator.userAgent.toLowerCase().includes("firefox");

    if (isFirefox) {
      return tooltip === "top" ? "28px" : "-24px";
    }

    return tooltip === "top" ? "16px" : "-32px";
  };

  const { _rangeMinimumAriaLabel, _rangeMaximumAriaLabel, _ariaLabel, _inputAriaLabel } =
    useCopy("Slider");

  return (
    <div
      data-testid={dataTestId && `${dataTestId}-container`}
      className={classNames(styles.container, className, {
        [styles.withScaleLegend]: scaleMarkers && withScaleLegend,
      })}
    >
      <div
        data-testid={dataTestId && `${dataTestId}-slider-container`}
        className={classNames(styles.sliderContainer, {
          [styles.rangeContainer]: !!range,
        })}
        style={{
          width,
        }}
      >
        {range ? (
          <>
            <input
              aria-label={_rangeMinimumAriaLabel}
              data-testid={dataTestId && `${dataTestId}-range-min`}
              type="range"
              id="min"
              min={min}
              max={max}
              step={step}
              value={value.min}
              onChange={handleChange}
            />
            <input
              aria-label={_rangeMaximumAriaLabel}
              data-testid={dataTestId && `${dataTestId}-range-max`}
              type="range"
              id="max"
              min={min}
              max={max}
              step={step}
              value={value.max}
              onChange={handleChange}
            />
          </>
        ) : (
          <input
            aria-label={_ariaLabel}
            data-testid={dataTestId && `${dataTestId}-slider`}
            type="range"
            min={min}
            max={max}
            step={step}
            value={value}
            onChange={handleChange}
          />
        )}
        {tooltip &&
          (range ? (
            <>
              <output
                data-testid={dataTestId && `${dataTestId}-tooltip-min`}
                style={
                  {
                    left: calculateThumbPosition(value.min),
                    "--val": value.min,
                    bottom: calculateOutputBottomOffset(),
                  } as CSSProperties
                }
              />
              <output
                data-testid={dataTestId && `${dataTestId}-tooltip-max`}
                style={
                  {
                    left: calculateThumbPosition(value.max),
                    "--val": value.max,
                    bottom: calculateOutputBottomOffset(),
                  } as CSSProperties
                }
              />
            </>
          ) : (
            <output
              data-testid={dataTestId && `${dataTestId}-tooltip`}
              style={
                {
                  left: calculateThumbPosition(value),
                  "--val": value,
                  bottom: calculateOutputBottomOffset(),
                } as CSSProperties
              }
            />
          ))}
        {range ? (
          <div
            data-testid={dataTestId && `${dataTestId}-filled-bar`}
            className={classNames(styles.filledBar, styles.filledBarRange)}
            // it has to be passed as css variables because wihtout it Safari will calculate only one style property at once
            style={
              {
                "--left": calculateThumbPosition(value.min),
                "--width": `calc(${calculateThumbPosition(value.max)} - ${calculateThumbPosition(
                  value.min
                )})`,
              } as CSSProperties
            }
          />
        ) : (
          <div
            data-testid={dataTestId && `${dataTestId}-filled-bar`}
            className={styles.filledBar}
            style={{ width: calculateThumbPosition(value) }}
          />
        )}
        {scaleMarkers && (
          <div className={styles.scaleMarkers}>
            {scaleLegendStepsArray.map((marker, idx) => (
              <div
                data-testid={dataTestId && `${dataTestId}-marker-${idx}`}
                className={styles.marker}
                key={marker}
              >
                {withScaleLegend && (
                  <span
                    data-testid={dataTestId && `${dataTestId}-marker-${idx}-legend`}
                    className={styles.markerLegend}
                  >
                    {marker}
                  </span>
                )}
              </div>
            ))}
          </div>
        )}
      </div>
      {withInput && (
        <label
          htmlFor="number-input"
          className={classNames(styles.numberInput, {
            [styles.percentageInput]: !!percentageInput,
          })}
        >
          <input
            data-testid={dataTestId && `${dataTestId}-input`}
            id="number-input"
            ref={inputRef}
            type="number"
            onKeyUp={(e) => {
              if (e.key === "Enter") {
                e.currentTarget.blur();
              }
            }}
            onFocus={(e) => e.target.select()}
            defaultValue={value.toFixed(decimalCount === 0 ? 0 : decimalCount)}
            onBlur={handleBlur}
            min={min}
            max={max}
            step={step}
            aria-label={_inputAriaLabel}
          />
        </label>
      )}
    </div>
  );
};
