import classNames from "classnames";
import React from "react";

import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import styles from "./Tag.module.scss";

export type DefaultTagVariant = "neutral-light" | "accent-blue" | "accent-green" | "accent-magenta";

export type TagProps = React.HTMLAttributes<
  HTMLSpanElement | HTMLButtonElement | HTMLInputElement
> & {
  dataTestId?: string;
  children?: React.ReactNode;
  className?: string;
} & (
    | {
        type?: "default";
        icon?: never;
        iconLeft?: never;
        iconRight?: never;
        size?: never;
        selected?: never;
        leftIcon?: Icons;
        variant?: DefaultTagVariant;
      }
    | {
        type: "filter";
        icon?: Icons;
        size?: "medium" | "small";
        selected?: boolean;
        variant?: never;
        iconLeft?: never;
        iconRight?: never;
        leftIcon?: never;
      }
    | {
        type: "choice";
        iconLeft?: Icons;
        iconRight?: Icons;
        size?: "medium" | "small";
        selected?: boolean;
        name: string;
        value: string;
        variant?: never;
        icon?: never;
        leftIcon?: never;
      }
  );

export const Tag: React.FC<TagProps> = ({ dataTestId, children, type = "default", ...props }) => {
  const commonClass = classNames(styles.container, styles[type]);

  if (type === "default") {
    const { className, variant = "neutral-light", leftIcon, ...rest } = props;
    const tagClass = classNames(commonClass, styles[variant], className);

    return (
      <span data-testid={dataTestId} className={tagClass} {...rest}>
        {leftIcon && <Icon iconName={leftIcon} size={16} />}
        {children}
      </span>
    );
  }

  if (type === "filter") {
    const { selected, size = "small", icon, ...rest } = props;
    const tagClass = classNames(commonClass, styles[size], {
      [styles.selected]: selected,
    });
    const iconSize = size === "small" ? 16 : 20;

    return (
      <button data-testid={dataTestId} className={classNames(tagClass, props.className)} {...rest}>
        {selected ? (
          <Icon dataTestId={`${dataTestId}-icon-left`} iconName={Icons.Check} size={iconSize} />
        ) : (
          <i data-testid={`${dataTestId}-icon-left`} className={styles.dot} />
        )}
        <span>{children}</span>
        {icon && <Icon dataTestId={`${dataTestId}-icon-right`} iconName={icon} size={iconSize} />}
      </button>
    );
  }

  if (type === "choice") {
    const { className, style, size = "small", iconLeft, iconRight, selected, ...rest } = props;
    const tagClass = classNames(commonClass, styles[size], className);
    const iconSize = size === "small" ? 16 : 20;

    return (
      <label data-testid={dataTestId} className={tagClass} style={style}>
        <input
          data-testid={`${dataTestId}-input`}
          type="radio"
          hidden
          checked={selected}
          {...rest}
        />
        {iconLeft && (
          <Icon dataTestId={`${dataTestId}-icon-left`} iconName={iconLeft} size={iconSize} />
        )}
        <span>{children}</span>
        {iconRight && (
          <Icon dataTestId={`${dataTestId}-icon-right`} iconName={iconRight} size={iconSize} />
        )}
      </label>
    );
  }

  return null;
};
