import classNames from "classnames";
import React from "react";

import { NakedButton } from "../../../shared/components/NakedButton/NakedButton";
import {
  Link as ClassicLink,
  LinkProps as ClassicLinkProps,
} from "../../../ui-components/Link/Link";
import styles from "./Link.module.scss";

export type LinkSize = "medium" | "small";

export type LinkVariant = "standard" | "highlighted" | "muted";

export interface LinkProps extends Omit<ClassicLinkProps, "className" | "description"> {
  variant?: LinkVariant;
  size?: LinkSize;
  underlined?: boolean;
}

export const Link: React.FC<LinkProps> = ({
  variant = "standard",
  size = "medium",
  underlined,
  ...classicLinkProps
}) => {
  const { url, onClick, dataTestId, children, name, page } = classicLinkProps;

  if (!url && !page)
    return (
      <NakedButton
        role="link"
        data-testid={dataTestId}
        className={classNames(styles.link, styles[variant], styles[size], {
          [styles.underlined]: underlined,
        })}
        onClick={onClick}
      >
        {children || name}
      </NakedButton>
    );

  return (
    <ClassicLink
      {...classicLinkProps}
      className={classNames(styles.link, styles[variant], styles[size], {
        [styles.underlined]: underlined,
      })}
    />
  );
};
