import classNames from "classnames";
import React from "react";

import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import styles from "./Checkbox.module.scss";

type CheckboxWithLabelProps = {
  label: React.ReactNode;
  caption?: string;
};

type CheckboxWithoutLabelProps = {
  label?: undefined;
  caption?: never;
};

type CheckboxCommonProps = {
  id?: string;
  name?: string;
  dataTestId?: string;
  value?: string;
  disabled?: boolean;
  label?: React.ReactNode;
  labelIconName?: Icons;
  checked: boolean;
  asRadio?: boolean;
  asSwitch?: boolean;
  onChange?: React.ChangeEventHandler<HTMLInputElement>;
  className?: string;
  ariaLabel?: string;
  additionalHtmlProps?: React.InputHTMLAttributes<HTMLInputElement>;
} & (CheckboxWithLabelProps | CheckboxWithoutLabelProps);

type NormalCheckboxProps = {
  wrappedInCard?: never;
  maxCardWidth?: never;
  instanceSlot?: never;
  hasCheckboxRight?: boolean;
} & CheckboxCommonProps;

type CheckboxWrappedInCardProps = {
  wrappedInCard?: boolean;
  maxCardWidth?: number;
  instanceSlot?: React.ReactNode;
  hasCheckboxRight?: never;
} & CheckboxCommonProps;

export type CheckboxProps = CheckboxWrappedInCardProps | NormalCheckboxProps;

export const Checkbox: React.FC<CheckboxProps> = ({
  id,
  name,
  dataTestId,
  value,
  disabled,
  label,
  labelIconName,
  caption,
  wrappedInCard,
  maxCardWidth = 352,
  checked,
  instanceSlot,
  asRadio,
  hasCheckboxRight,
  asSwitch,
  onChange,
  className,
  ariaLabel,
  additionalHtmlProps,
}) => (
  <div
    className={classNames(styles.container, className, { [styles.withFocus]: wrappedInCard })}
    data-testid={dataTestId}
    style={wrappedInCard && maxCardWidth ? { maxWidth: maxCardWidth } : {}}
  >
    <label
      data-testid={dataTestId && `${dataTestId}-label`}
      className={classNames(styles.checkboxLabel, { [styles.wrappedInCard]: wrappedInCard })}
    >
      <div
        className={classNames(styles.row, {
          [styles.reverse]: wrappedInCard || hasCheckboxRight,
          [styles.biggerGap]: wrappedInCard || (hasCheckboxRight && caption),
          [styles.centerItems]: !wrappedInCard,
        })}
      >
        <CheckboxInput
          id={id}
          name={name}
          dataTestId={dataTestId}
          value={value}
          checked={checked}
          disabled={disabled}
          asRadio={asRadio}
          asSwitch={asSwitch}
          onChange={onChange}
          ariaLabel={typeof label === "string" ? label ?? ariaLabel : ariaLabel}
          additionalHtmlProps={additionalHtmlProps}
        />
        <div className={styles.labelContainer}>
          {(labelIconName || label) && (
            <div className={styles.labelIconAndLabel}>
              {labelIconName && <Icon className={styles.labelIcon} iconName={labelIconName} />}
              {label && <div className={styles.label}>{label}</div>}
            </div>
          )}
          {(label || labelIconName) && caption && <div className={styles.caption}>{caption}</div>}
        </div>
      </div>
      {wrappedInCard && instanceSlot && (
        <div data-testid={dataTestId && `${dataTestId}-instance-slot`}>{instanceSlot}</div>
      )}
    </label>
  </div>
);

interface CheckboxInputProps {
  id?: string;
  name?: string;
  dataTestId?: string;
  value?: string;
  checked: boolean;
  disabled?: boolean;
  asRadio?: boolean;
  asSwitch?: boolean;
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
  ariaLabel?: string;
  additionalHtmlProps?: React.InputHTMLAttributes<HTMLInputElement>;
}

const CheckboxInput = ({
  id,
  name,
  dataTestId,
  value,
  checked,
  disabled,
  asRadio,
  asSwitch,
  onChange,
  ariaLabel,
  additionalHtmlProps,
}: CheckboxInputProps) => (
  <div className={styles.checkboxInput}>
    <div className={classNames(styles.checkboxFocusFrame, { [styles.rounded]: asRadio })}>
      {!asSwitch && checked ? (
        <Icon iconName={asRadio ? Icons.RadioCheckboxSelected : Icons.CheckboxSelected} />
      ) : (
        <div
          className={classNames(styles.checkboxFrame, {
            [styles.rounded]: asRadio,
          })}
        >
          {asSwitch && (
            <div className={classNames(styles.switchControl, { [styles.turnedOn]: checked })} />
          )}
        </div>
      )}
    </div>
    <input
      id={id}
      name={name}
      aria-label={ariaLabel}
      data-testid={dataTestId && `${dataTestId}-input`}
      className={styles.invisibleInput}
      type="checkbox"
      value={value}
      aria-checked={checked}
      disabled={disabled}
      checked={checked}
      onChange={onChange}
      {...additionalHtmlProps}
    />
  </div>
);
