import classNames from "classnames";
import React, { MouseEventHandler } from "react";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import styles from "./Button.module.scss";

export type ButtonProps = {
  additionalHtmlProps?: Omit<
    React.ButtonHTMLAttributes<HTMLButtonElement>,
    "className" | "style" | "disabled" | "onClick" | "color"
  >;
  children?: React.ReactNode;
  dataTestId?: string;
  variant?: "primary" | "secondary" | "outline" | "destructive" | "contrast" | "ghost" | "split";
  size?: "small" | "medium" | "large";
  iconLeft?: Icons;
  iconRight?: Icons;
  splitIcon?: Icons;
  iconColor?: Colors;
  className?: string;
  style?: React.CSSProperties;
  disabled?: boolean;
  loading?: boolean;
  onClick?: MouseEventHandler<HTMLButtonElement>;
};

export const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(
  (
    {
      additionalHtmlProps,
      children,
      dataTestId,
      variant = "primary",
      size = "medium",
      iconLeft,
      iconRight,
      splitIcon,
      iconColor,
      className,
      loading,
      disabled,
      ...props
    },
    ref
  ) => {
    const buttonClass = classNames(styles.button, styles[variant], styles[size], className, {
      [styles.loading]: loading,
    });

    const spinnerIcon = (
      <Icon
        dataTestId={dataTestId && `${dataTestId}-spinner-icon`}
        className={classNames(styles.spinner, styles.icon, styles[variant])}
        iconName={Icons.Spinner}
        color={Colors.ContentAccentGreen}
      />
    );

    // Icon button
    if (iconLeft || iconRight) {
      const iconButtonClass = classNames(buttonClass, {
        [styles.iconLeft]: iconLeft,
        [styles.iconRight]: iconRight,
      });

      const getIconColor = () => {
        if (!disabled) return iconColor;

        switch (variant) {
          case "primary":
            return Colors.SurfacePrimaryContrast;
          case "secondary":
          case "outline":
          case "ghost":
          case "split":
            return Colors.ContentPrimaryDisabled;
          case "destructive":
            return Colors.ContentPrimaryContrast;
        }
      };

      const renderIcon = (icon?: Icons) =>
        icon ? (
          loading ? (
            spinnerIcon
          ) : (
            <Icon
              dataTestId={dataTestId && `${dataTestId}-icon`}
              className={styles.icon}
              color={getIconColor()}
              iconName={icon}
            />
          )
        ) : (
          ""
        );

      return (
        <button
          ref={ref}
          data-testid={dataTestId}
          className={iconButtonClass}
          disabled={disabled || loading}
          {...props}
          {...additionalHtmlProps}
        >
          {renderIcon(iconLeft)}
          <span className={styles.text}>{children}</span>
          {variant === "split" ? (
            <SplitIcon dataTestId={dataTestId} icon={splitIcon} />
          ) : (
            renderIcon(iconRight)
          )}
        </button>
      );
    }

    return (
      <button
        ref={ref}
        data-testid={dataTestId}
        className={buttonClass}
        disabled={disabled || loading}
        {...props}
        {...additionalHtmlProps}
      >
        <div className={styles.spinnerAndText}>
          {loading && <div className={styles.spinnerContainer}>{spinnerIcon}</div>}
          <span className={classNames(styles.text, { [styles.hidden]: loading })}>{children}</span>
        </div>
        {variant === "split" && <SplitIcon dataTestId={dataTestId} icon={splitIcon} />}
      </button>
    );
  }
);

interface SplitIconProps {
  dataTestId?: string;
  icon?: Icons;
}

const SplitIcon = ({ dataTestId, icon }: SplitIconProps) => (
  <Icon
    dataTestId={dataTestId && `${dataTestId}-split-icon`}
    className={classNames(styles.icon, styles.splitIcon)}
    iconName={icon || Icons.AngleDown}
  />
);
