import classNames from "classnames";
import React, { useCallback, useRef, useState } from "react";
import { useDebouncedCallback } from "use-debounce";

import { useCopy } from "../../../extensions";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import styles from "./NumberStepper.module.scss";

export interface NumberStepperProps {
  dataTestId?: string;
  min?: number;
  max?: number;
  step?: number;
  editable?: boolean;
  defaultValue?: number;
  value?: number | null;
  onChange?: (value: number | null) => void;
  // uncontrolled only, controlled input should handle it inside passed onChange prop,
  // otherwise we would end up with non instant changing values on user input / button clicks which is bad
  debounce?: number;
  suppressWarnings?: boolean;
  suppressErrors?: boolean;
  ariaLabel?: string;
}

export const NumberStepper: React.FC<NumberStepperProps> = ({
  dataTestId,
  min = 1,
  max = 99,
  step = 1,
  editable = false,
  defaultValue,
  value,
  onChange,
  debounce,
  suppressWarnings = false,
  suppressErrors = false,
  ariaLabel,
}) => {
  if (!suppressErrors && (max < min || min > max))
    throw new Error("min and/or max value is incorrect!");
  if (!suppressWarnings && defaultValue !== undefined && value !== undefined)
    console.warn("defaultValue is ignored when value prop is passed");
  if (!suppressWarnings && debounce !== undefined && value !== undefined)
    console.warn(
      "debounce is ignored by controlled input as value changes only by onChange method and debouncing it will make input UX really bad"
    );

  // used to catch uncontrolled input state for button disabling
  const [inputValue, setInputValue] = useState<number | null>(defaultValue ?? min);
  const inputRef = useRef<HTMLInputElement>(null);
  const isControlled = value !== undefined;

  const { _ariaLabel } = useCopy("NumberStepper");

  const debouncedOnChange = useDebouncedCallback((val) => {
    onChange?.(val);
  }, debounce);

  const ensureMinMaxBoundaries = useCallback(
    (val: number | null) => {
      if (val === null) return null;

      if (val < min) {
        return min;
      }

      if (val > max) {
        return max;
      }

      return val;
    },
    [min, max]
  );

  const computeValue = (changeType: "INCREMENT" | "DECREMENT", val: number | null) => {
    if (val === null) return min;

    const isValueOverOrEqualMax = val >= max;
    const isValueBelowOrEqualMin = val <= min;

    if (changeType === "INCREMENT" ? isValueOverOrEqualMax : isValueBelowOrEqualMin) return null;

    const incremented = ensureMinMaxBoundaries(val + step);
    const decremented = ensureMinMaxBoundaries(val - step);

    return changeType === "INCREMENT" ? incremented : decremented;
  };

  const changeValue = (changeType: "INCREMENT" | "DECREMENT") => {
    if (isControlled) {
      const newValue = computeValue(changeType, value);

      if (newValue === null) return;

      onChange?.(newValue);
    } else {
      const currentInputValue = inputRef.current?.valueAsNumber;

      if (!inputRef.current || currentInputValue === undefined) return;

      const newValue = computeValue(changeType, currentInputValue);

      if (newValue === null) return;

      inputRef.current.value = newValue.toString();

      if (debounce) {
        debouncedOnChange(newValue);
      } else {
        onChange?.(newValue);
      }

      setInputValue(newValue);
    }
  };

  const handleBlur = (e: React.FocusEvent<HTMLInputElement, Element>) => {
    // handle empty input on blur
    if (!e.target.value) {
      if (isControlled) {
        onChange?.(min);
      } else if (inputRef.current) {
        inputRef.current.value = min.toString();

        if (debounce) {
          debouncedOnChange(min);
        } else {
          onChange?.(min);
        }

        setInputValue(min);
      }

      return;
    }

    const parsedValue = isNaN(e.target.valueAsNumber) ? null : e.target.valueAsNumber;
    const newValue = ensureMinMaxBoundaries(parsedValue);

    onChange?.(newValue);

    if (!isControlled && inputRef.current) {
      inputRef.current.value = newValue?.toString() || "";
    }
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const parsedValue = isNaN(e.target.valueAsNumber) ? null : e.target.valueAsNumber;
    const newValue = ensureMinMaxBoundaries(parsedValue);

    if (isControlled && onChange) {
      onChange(newValue);
    } else {
      if (debounce) {
        debouncedOnChange(newValue);
      } else {
        onChange?.(newValue);
      }

      setInputValue(parsedValue);
    }
  };

  return (
    <div className={styles.container}>
      <Icon
        className={classNames({
          [styles.disabled]: (isControlled ? value ?? min : inputValue ?? min) <= min,
        })}
        dataTestId={dataTestId && `${dataTestId}-quantity-decrement`}
        iconName={Icons.Minus}
        size={20}
        onClick={() => changeValue("DECREMENT")}
      />
      <input
        aria-label={ariaLabel ?? _ariaLabel}
        ref={inputRef}
        className={classNames({
          [styles.editable]: editable,
        })}
        data-testid={dataTestId && `${dataTestId}-quantity-input`}
        type="number"
        onBlur={handleBlur}
        onChange={handleChange}
        value={isControlled ? value ?? "" : value}
        min={min}
        onKeyDown={(e) => ["e", "E", "+", "-", ",", "."].includes(e.key) && e.preventDefault()}
        max={max}
        defaultValue={isControlled ? undefined : defaultValue || min}
      />
      <Icon
        className={classNames({
          [styles.disabled]: (isControlled ? value ?? min : inputValue ?? min) >= max,
        })}
        dataTestId={dataTestId && `${dataTestId}-quantity-increment`}
        iconName={Icons.Plus}
        size={20}
        onClick={() => changeValue("INCREMENT")}
      />
    </div>
  );
};
