import classNames from "classnames";
import React, { MouseEventHandler, useEffect, useState } from "react";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { Badge } from "../Badge/Badge";
import { Icon } from "../Icon/Icon";
import { Tooltip } from "../Tooltip/Tooltip";
import styles from "./IconButton.module.scss";

export type IconButtonProps = {
  additionalHtmlProps?: Omit<
    React.ButtonHTMLAttributes<HTMLButtonElement>,
    "className" | "style" | "disabled" | "onClick" | "color"
  >;
  dataTestId?: string;
  iconName: Icons;
  variant?: "default" | "outline" | "ghost";
  shift?: "right" | "left";
  size?: "large" | "medium" | "small";
  badge?: number;
  selected?: boolean;
  iconColor?: Colors;
  className?: string;
  style?: React.CSSProperties;
  disabled?: boolean;
  onClick?: MouseEventHandler<HTMLButtonElement>;
  iconSize?: number;
  tooltip?: string;
  loading?: boolean;
} & (
  | { variant?: "default" | "outline"; ghostAccent?: never }
  | {
      variant: "ghost";
      ghostAccent?: Colors;
    }
);

export const IconButton = React.forwardRef<HTMLButtonElement, IconButtonProps>(
  (
    {
      additionalHtmlProps,
      dataTestId,
      iconName,
      variant = "default",
      shift,
      size = "large",
      badge,
      selected,
      iconColor = Colors.ContentPrimaryEmphasized,
      className,
      style,
      ghostAccent,
      iconSize = 24,
      tooltip,
      loading,
      ...props
    },
    ref
  ) => {
    const [id, setId] = useState("");

    useEffect(() => {
      let id;
      try {
        id = crypto.randomUUID();
      } catch {
        // native crypto not available; fallback to simple solution.
        id = Math.random() * new Date().getTime();
      }
      setId(id.toString());
    }, []);

    const iconButtonClass = classNames(
      styles.button,
      styles[variant],
      styles[size],
      selected ? styles.selected : undefined,
      {
        [styles.shiftLeft]: shift === "left",
        [styles.shiftRight]: shift === "right",
      },
      className
    );

    const customColors = {
      "--color": iconColor,
      "--ghostAccent": ghostAccent,
    } as React.CSSProperties;

    const tooltipProps = tooltip
      ? {
          "data-tip": true,
          "data-for": id,
        }
      : {};

    return (
      <>
        <button
          ref={ref}
          className={classNames(styles.button, iconButtonClass)}
          style={{ ...(customColors || {}), ...style }}
          data-testid={dataTestId}
          disabled={loading ? true : undefined}
          {...tooltipProps}
          {...props}
          {...additionalHtmlProps}
        >
          {loading ? (
            <Icon
              dataTestId={dataTestId && `${dataTestId}-spinner-icon`}
              className={classNames(styles.spinner, styles.icon, styles[variant])}
              iconName={Icons.Spinner}
              size={size === "small" ? 12 : iconSize}
            />
          ) : (
            <Icon
              dataTestId={`${dataTestId}-icon`}
              className={styles.icon}
              iconName={iconName}
              size={iconSize}
            />
          )}

          {(badge ?? 0) > 0 && (
            <Badge dataTestId={`${dataTestId}-badge`} className={styles.badge} number={badge} />
          )}
        </button>
        {tooltip && id && <Tooltip tooltipId={id}>{tooltip}</Tooltip>}
      </>
    );
  }
);
