import classNames from "classnames";
import React from "react";

import { Colors } from "../../constants";
import { useMediaQuerySmallerThan } from "../../hooks";
import { Icons } from "../../icon-types";
import { Button, ButtonProps } from "../Button/Button";
import { Icon } from "../Icon/Icon";
import styles from "./Card.module.scss";

export type BasicCardType = "action" | "positive" | "negative" | "neutral" | "info";

type Cta = {
  action: () => void;
  title: string;
} & Omit<ButtonProps, "onClick" | "className" | "dataTestId">;

export interface BasicCardProps {
  dataTestId?: string;
  type?: BasicCardType;
  title?: string;
  content: React.ReactNode;
  cta?: Cta;
}

export const BasicCard: React.FC<BasicCardProps> = ({
  dataTestId,
  type = "action",
  title,
  content,
  cta,
}) => {
  const isSmallerThanXS = useMediaQuerySmallerThan("XS");

  const { title: ctaTitle, action, ...buttonProps } = cta || {};

  const ctaBtn = (
    <Button
      dataTestId={dataTestId ? `${dataTestId}-button` : undefined}
      className={styles.ctaBtn}
      variant="contrast"
      onClick={action}
      {...buttonProps}
    >
      {ctaTitle}
    </Button>
  );

  return (
    <div data-testid={dataTestId} className={classNames(styles.card, styles[type])}>
      {(title || cta) && (
        <div className={styles.header}>
          {title && <h3 className={styles.title}>{title}</h3>}
          {cta && !isSmallerThanXS && ctaBtn}
        </div>
      )}
      <div className={styles.content}>{content}</div>
      {cta && isSmallerThanXS && ctaBtn}
    </div>
  );
};

export interface CardWithImageProps {
  dataTestId?: string;
  label: string;
  image?: string;
  textLine1: React.ReactNode;
  textLine2?: React.ReactNode;
}

export const CardWithImage: React.FC<CardWithImageProps> = ({
  dataTestId,
  label,
  image,
  textLine1,
  textLine2,
}) => (
  <div data-testid={dataTestId} className={styles.cardWithImage}>
    <div className={styles.imageContainer}>
      {image ? (
        <img
          data-testid={dataTestId && `${dataTestId}-image`}
          className={styles.image}
          src={image}
          alt={label}
        />
      ) : (
        <div className={classNames(styles.placeholder)}>
          <Icon
            dataTestId={dataTestId && `${dataTestId}-placeholder`}
            color={Colors.ContentPrimaryMuted}
            iconName={Icons.Picture}
          />
        </div>
      )}
    </div>
    <div className={styles.content}>
      <span className={styles.label}>{label}</span>
      <div className={styles.textLines}>
        <span>{textLine1}</span>
        {textLine2 && <span>{textLine2}</span>}
      </div>
    </div>
  </div>
);

export interface ModularCardProps {
  dataTestId?: string;
  instanceSlot?: React.ReactNode;
  title?: string;
  content?: React.ReactNode;
  cta?: Cta[];
  isBoxed?: boolean;
}

export const ModularCard: React.FC<ModularCardProps> = ({
  dataTestId,
  instanceSlot,
  title,
  content,
  cta,
  isBoxed,
}) => (
  <div
    data-testid={dataTestId}
    className={classNames(styles.modularCard, { [styles.isBoxed]: isBoxed })}
  >
    {instanceSlot && <div className={styles.instanceSlot}>{instanceSlot}</div>}
    {title && <div className={styles.title}>{title}</div>}
    {content && <div className={styles.content}>{content}</div>}
    {cta && (
      <div className={styles.ctaContainer}>
        {cta.map(({ title, action, ...buttonProps }, index) => (
          <Button
            key={index}
            {...buttonProps}
            dataTestId={dataTestId && `${dataTestId}-cta-btn-${index + 1}`}
            className={styles.ctaButton}
            onClick={action}
          >
            {title}
          </Button>
        ))}
      </div>
    )}
  </div>
);
