import classNames from "classnames";
import React, { useContext, useEffect } from "react";
import { createPortal } from "react-dom";

import { Colors } from "../../constants";
import { ThemeContext } from "../../contexts/ThemeContext";
import { useMediaQuerySmallerThan } from "../../hooks";
import { Icons } from "../../icon-types";
import { Button, ButtonProps } from "../Button/Button";
import { Checkbox, CheckboxProps } from "../Checkbox/Checkbox";
import { Icon } from "../Icon/Icon";
import { IconButton } from "../IconButton/IconButton";
import styles from "./Modal.module.scss";

type Cta = {
  action: () => void;
  title: string;
} & Omit<ButtonProps, "onClick" | "className" | "dataTestId">;

export interface ModalProps {
  dataTestId?: string;
  cta: Cta[];
  isOpen: boolean;
  onClose: () => void;
  title: string;
  titleIconName?: Icons;
  maxWidth?: number | string;
  portalContainer?: HTMLElement;
  content?: string;
  hideCloseButton?: boolean;
  bodyInstanceSlot?: React.ReactNode;
  headerInstanceSlot?: React.ReactNode;
  checkboxProps?: CheckboxProps;
  modalClassName?: string;
  footerClassName?: string;
}

export const Modal: React.FC<ModalProps> = ({
  dataTestId,
  content,
  cta,
  onClose,
  title,
  maxWidth = 490,
  portalContainer,
  titleIconName,
  isOpen,
  hideCloseButton,
  bodyInstanceSlot,
  headerInstanceSlot,
  checkboxProps,
  modalClassName,
  footerClassName,
}) => {
  const isSmallerThanS = useMediaQuerySmallerThan("S");
  const { theme } = useContext(ThemeContext);

  useEffect(() => {
    isOpen ? lockScroll() : unlockScroll();
    return () => unlockScroll();
  }, [isOpen]);

  if (!isOpen) return null;

  return createPortal(
    <div
      role="presentation"
      data-testid={`${dataTestId}-backdrop`}
      className={styles.backdrop}
      onMouseDown={(e) => e.target === e.currentTarget && onClose()}
    >
      <div
        role="presentation"
        data-testid={dataTestId}
        className={classNames(styles.modal, modalClassName, theme)}
        style={{ maxWidth }}
        onClick={(e) => e.stopPropagation()}
      >
        <div className={classNames(styles.header, isSmallerThanS ? "h8-semibold" : "h7-medium")}>
          {headerInstanceSlot && (
            <div className={styles.headerInstanceSlot}>{headerInstanceSlot}</div>
          )}
          <div
            className={classNames(styles.title, {
              [styles.alignCenter]: headerInstanceSlot,
            })}
          >
            {titleIconName && (
              <Icon
                className={styles.titleIcon}
                dataTestId={`${dataTestId}-title-icon`}
                iconName={titleIconName}
              />
            )}
            {title}
          </div>
          {!hideCloseButton && (
            <IconButton
              dataTestId={dataTestId && `${dataTestId}-close-btn`}
              className={styles.closeBtn}
              onClick={onClose}
              iconName={Icons.Cross}
              iconColor={Colors.ContentPrimaryEmphasized}
            />
          )}
        </div>
        <div
          className={classNames(styles.content, isSmallerThanS ? "b3-medium" : "b2-medium", {
            [styles.alignCenter]: headerInstanceSlot,
          })}
        >
          {content}
        </div>
        {bodyInstanceSlot && bodyInstanceSlot}
        <div className={classNames(styles.footer, footerClassName)}>
          {checkboxProps && <Checkbox {...checkboxProps} />}
          <div className={styles.ctaContainer}>
            {cta.map(({ title, action, ...buttonProps }, index) => (
              <Button
                key={index}
                {...buttonProps}
                dataTestId={dataTestId && `${dataTestId}-cta-btn-${index + 1}`}
                className={styles.ctaButton}
                onClick={action}
              >
                {title}
              </Button>
            ))}
          </div>
        </div>
      </div>
    </div>,
    portalContainer ?? document.body
  );
};

const unlockScroll = () => {
  document.body.style.removeProperty("overflow");
};

const lockScroll = () => {
  document.body.style.overflow = "hidden";
};
