import React from "react";
import ReactSelect, { GroupBase, Props as ReactSelectProps } from "react-select";

import { useCopy } from "../../../extensions";
import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import { Tooltip } from "../Tooltip/Tooltip";
import styles from "./Select.module.scss";

export interface FormSelectProps {
  dataTestId?: string;
  error?: boolean | string;
  label?: string;
  variant?: "filled" | "outline";
  tooltip?: string;
}

export const Select = <
  Option,
  IsMulti extends boolean = false,
  Group extends GroupBase<Option> = GroupBase<Option>
>({
  dataTestId,
  error,
  label,
  variant = "filled",
  tooltip,
  ...selectProps
}: FormSelectProps & ReactSelectProps<Option, IsMulti, Group>) => {
  const { _textOptional } = useCopy("Input");
  const { _textNoResults, _textLoading } = useCopy("CustomSelect");

  const borderColor = variant === "outline" ? "#5D6A75" : "transparent";

  const customStyles: ReactSelectProps<Option, IsMulti, Group>["styles"] = {
    container: (base, props) => ({
      ...base,
      fontFamily: "museo-sans, sans-serif",
      fontSize: "0.875rem",
      fontWeight: 500,
      lineHeight: "1.125rem",
    }),
    control: (base, props) => ({
      ...base,
      borderRadius: "0.5rem",
      borderWidth: "1px",
      borderColor: error ? "#D83542" : props.isFocused ? "#564fd0" : borderColor,
      boxShadow: props.isFocused ? "0 0 0 2px #e8e7f4" : "none",
      minHeight: 40,
      backgroundColor: "#ffffff",
      "&:hover [class$='placeholder']": {
        color: "#505f6b",
      },
      "&:hover": !props.isFocused
        ? {
            borderColor: borderColor,
          }
        : undefined,
    }),
    singleValue: (base, props) => ({
      ...base,
      color: props.isDisabled ? "#cccfd1" : "#303a4d",
    }),
    placeholder: (base, props) => ({
      ...base,
      color: props.isDisabled ? "#cccfd1" : "#6d7880",
    }),
    menu: (base, props) => ({
      ...base,
      padding: 16,
    }),
    option: (base, props) => ({
      ...base,
      padding: "8px 20px",
      backgroundColor: props.isSelected
        ? "transparent"
        : props.isFocused
        ? "#F5F5F5"
        : "transparent",
      color: props.isDisabled ? "#CCCFD1" : props.isSelected ? "#564FD0" : "inherit",
      // provide some affordance on touch devices
      ":active": {
        backgroundColor: !props.isDisabled ? "#D8DADB" : undefined,
        color: !props.isDisabled ? "#564FD0" : "inherit",
      },
    }),
    indicatorSeparator: (base, props) => ({
      ...base,
      visibility: "hidden",
    }),
    dropdownIndicator: (base, props) => ({
      ...base,
      color: props.isDisabled ? "#CCCFD1" : "#5D6A75",
      transition: "transform 300ms ease-in-out",
      transform: props.selectProps.menuIsOpen ? "rotate(180deg)" : undefined,
    }),
  };

  return (
    <div data-testid={dataTestId && `${dataTestId}-select`} className={styles.wrapper}>
      {label && (
        <div className={styles.label}>
          {label}{" "}
          {!selectProps.required && <span className={styles.optional}>{_textOptional}</span>}
          {tooltip && (
            <>
              <span className={styles.tooltipIcon} data-tip data-for={`${label}-tooltip`}>
                <Icon
                  dataTestId={`${dataTestId}-tooltip-icon`}
                  color={Colors.ContentPrimaryMuted}
                  size={20}
                  iconName={Icons.Info}
                />
              </span>
              <Tooltip dataTestId={`${dataTestId}-tooltip`} tooltipId={`${label}-tooltip`}>
                {tooltip}
              </Tooltip>
            </>
          )}
        </div>
      )}
      <ReactSelect
        styles={customStyles}
        noOptionsMessage={() => _textNoResults}
        loadingMessage={() => _textLoading}
        {...selectProps}
      />
      {typeof error === "string" && (
        <div className={styles.error}>
          <Icon
            dataTestId={`${dataTestId}-error-icon`}
            color={Colors.ContentErrorEmphasized}
            size={20}
            iconName={Icons.AlertFilled}
          />
          <span>{error}</span>
        </div>
      )}
    </div>
  );
};
