import classNames from "classnames";
import React, { forwardRef, useState } from "react";

import { useCopy } from "../../../extensions";
import { NakedButton } from "../../../shared/components/NakedButton/NakedButton";
import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import { Tooltip } from "../Tooltip/Tooltip";
import styles from "./Input.module.scss";

type Currency = "USD" | "CAD" | "EUR" | "AUD" | "GBP";

export interface BaseInputProps {
  additionalHtmlProps?: Omit<
    React.InputHTMLAttributes<HTMLInputElement>,
    "id" | "name" | "className" | "style" | "placeholder" | "disabled" | "value" | "required"
  >;
  id?: string;
  name?: string;
  dataTestId?: string;
  disabled?: boolean;
  placeholder?: string;
  error?: boolean | string;
  value?: string;
  onChange?: React.ChangeEventHandler<HTMLInputElement>;
  onFocus?: React.FocusEventHandler<HTMLInputElement>;
  onBlur?: React.FocusEventHandler<HTMLInputElement>;
  onKeyUp?: React.KeyboardEventHandler<HTMLInputElement>;
  style?: React.CSSProperties;
  icon?: Icons;
  size?: "medium" | "small";
  required?: boolean;
  label?: string;
  variant?: "filled" | "outline";
  tooltip?: string;
  description?: string;
  ariaLabel?: string;
  currencyPrefix?: Currency;
}

interface InputWithCurrencyPrefix extends BaseInputProps {
  currencyPrefix?: Currency;
  icon?: never;
}

interface InputWithIconPrefix extends BaseInputProps {
  icon?: Icons;
  currencyPrefix?: never;
}

export type InputProps = InputWithCurrencyPrefix | InputWithIconPrefix;

export const Input = forwardRef<HTMLInputElement, InputProps>(
  (
    {
      additionalHtmlProps,
      currencyPrefix,
      placeholder,
      description,
      dataTestId,
      disabled,
      required,
      onChange,
      onFocus,
      onBlur,
      onKeyUp,
      variant = "filled",
      tooltip,
      error,
      value,
      style,
      label,
      icon,
      name,
      id,
      size = "medium",
      ariaLabel,
    },
    ref
  ) => {
    const { _textOptional, _textShow, _textHide, _textShowAriaLabel, _textHideAriaLabel } =
      useCopy("Input");
    const [showPassword, setShowPassword] = useState(false);

    const isPasswordInput = additionalHtmlProps?.type === "password";

    return (
      <div className={styles.wrapper}>
        {label && (
          <div className={styles.label}>
            {label} {!required && <span className={styles.optional}>{_textOptional}</span>}
            {tooltip && (
              <>
                <span className={styles.tooltipIcon} data-tip data-for={`${label}-tooltip`}>
                  <Icon
                    dataTestId={`${dataTestId}-tooltip-icon`}
                    color={Colors.ContentPrimaryMuted}
                    size={20}
                    iconName={Icons.Info}
                  />
                </span>
                <Tooltip dataTestId={`${dataTestId}-tooltip`} tooltipId={`${label}-tooltip`}>
                  {tooltip}
                </Tooltip>
              </>
            )}
          </div>
        )}
        <div className={styles.inputContainer}>
          <input
            ref={ref}
            id={id}
            name={name}
            data-testid={dataTestId}
            className={classNames(styles.input, styles[variant], {
              [styles.errorInput]: !!error,
              [styles.small]: size === "small",
              [styles.hasPasswordToggle]: isPasswordInput,
            })}
            placeholder={placeholder}
            disabled={disabled}
            value={value}
            style={style}
            required={required}
            onChange={onChange}
            onFocus={onFocus}
            onBlur={onBlur}
            onKeyUp={onKeyUp}
            aria-label={ariaLabel || label}
            {...additionalHtmlProps}
            type={
              isPasswordInput ? (showPassword ? "text" : "password") : additionalHtmlProps?.type
            }
          />
          {icon && <Icon dataTestId={`${dataTestId}-icon`} iconName={icon} />}
          {currencyPrefix && <span>{currencyPrefix}</span>}
          {isPasswordInput && (
            <NakedButton
              type="button"
              className={styles.passwordToggle}
              onClick={() => setShowPassword((prevState) => !prevState)}
              data-testid={`${dataTestId}-password-toggle`}
              aria-label={showPassword ? _textHideAriaLabel : _textShowAriaLabel}
            >
              {showPassword ? _textHide : _textShow}
            </NakedButton>
          )}
        </div>
        {description && <div className={styles.description}>{description}</div>}
        {typeof error === "string" && (
          <div className={styles.error}>
            <Icon
              dataTestId={`${dataTestId}-error-icon`}
              color={Colors.ContentErrorEmphasized}
              size={20}
              iconName={Icons.AlertFilled}
            />
            <span>{error}</span>
          </div>
        )}
      </div>
    );
  }
);
