import classNames from "classnames";
import React, { useEffect, useLayoutEffect, useRef, useState } from "react";

import { NakedButton } from "../../../shared/components/NakedButton/NakedButton";
import { isEnterOrSpaceKeyEvent } from "../../../utils/isEnterOrSpaceKeyEvent";
import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { Button } from "../Button/Button";
import { Icon } from "../Icon/Icon";
import styles from "./Dropdown.module.scss";

type Size = "small" | "medium";
type OpeningDirection = "down" | "up";

export type Item<T> = {
  label: string;
  value: T;
  leftIcon?: Icons;
  rightIcon?: Icons;
};

interface SharedProps<T> {
  dataTestId?: string;
  size?: Size;
  initialOpeningDirection?: OpeningDirection;
  onSelect: (value: T) => void;
  items: Item<T>[];
}

interface BaseActionableVariantProps {
  variant?: "actionable";
  leftIcon?: never;
  defaultValue?: never;
  labelColor?: never;
}

type ActionableVariantProps =
  | (BaseActionableVariantProps & {
      asSplitButton: true;
      label: string;
    })
  | (BaseActionableVariantProps & {
      asSplitButton?: false;
      label?: never;
    });

interface SelectableVariantProps<T> {
  variant?: "selectable";
  leftIcon?: Icons;
  defaultValue?: T;
  label?: string;
  labelColor?: Colors;
  asSplitButton?: never;
}

export type SelectProps<T> =
  | (SharedProps<T> & ActionableVariantProps)
  | (SharedProps<T> & SelectableVariantProps<T>);

export const Dropdown = <T,>({
  dataTestId,
  variant = "selectable",
  size = "medium",
  initialOpeningDirection = "down",
  leftIcon,
  label,
  labelColor,
  onSelect,
  defaultValue,
  items,
  asSplitButton,
}: SelectProps<T>) => {
  const [direction, setDirection] = useState<OpeningDirection>(initialOpeningDirection);
  const [isSelected, setIsSelected] = useState(false);
  const [selectedItem, setSelectedItem] = useState(
    items.find(({ value }) => value === defaultValue)
  );
  const selectRef = useRef<HTMLDivElement>(null);
  const dropdownRef = useRef<HTMLDivElement>(null);

  const calculateDropdownPosition = () => {
    if (selectRef.current && dropdownRef.current) {
      const selectRect = selectRef.current.getBoundingClientRect();
      const dropdownRect = dropdownRef.current.getBoundingClientRect();
      const viewportWidth = window.innerWidth;
      const boxShadowWidth = 3;
      let marginLeft = 0;

      // Calculate horizontal position
      if (selectRect.left + dropdownRect.width > viewportWidth) {
        // Adjust to the left if it overflows the right edge
        marginLeft = viewportWidth - (selectRect.left + dropdownRect.width) + 2 * boxShadowWidth;
      }

      if (dropdownRect.left < 0) {
        // Adjust to the right if it overflows the left edge
        marginLeft = -dropdownRect.left + boxShadowWidth;
      }

      // Calculate vertical direction
      const dropdownHeight = dropdownRef.current.offsetHeight;
      const spaceBelow = window.innerHeight - selectRect.bottom;
      const direction = spaceBelow < dropdownHeight ? "up" : "down";

      dropdownRef.current.style.marginLeft = `${marginLeft}px`;
      setDirection(direction);
    }
  };

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (selectRef.current && !selectRef.current.contains(event.target as Node)) {
        setIsSelected(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  useLayoutEffect(() => {
    if (isSelected && !asSplitButton) calculateDropdownPosition();
  }, [isSelected, asSplitButton]);

  return (
    <div className={styles.dropdownContainer} ref={selectRef}>
      {asSplitButton ? (
        <Button
          data-testid={dataTestId}
          variant="split"
          size={size}
          splitIcon={isSelected ? Icons.AngleUp : Icons.AngleDown}
          onClick={(event) => {
            event.stopPropagation();
            setIsSelected((v) => !v);
          }}
        >
          {label}
        </Button>
      ) : (
        <div
          tabIndex={0}
          id={dataTestId}
          data-testid={dataTestId}
          role="combobox"
          aria-expanded={isSelected}
          aria-haspopup="listbox"
          aria-controls={`${dataTestId}-dropdown`}
          aria-labelledby={`${dataTestId}-label`}
          className={classNames(styles.dropdownSelect, styles[size], {
            [styles.selected]: isSelected,
            [styles.withLeftIcon]: leftIcon,
            [styles.fullRounded]: variant === "actionable",
          })}
          onClick={(event) => {
            event.stopPropagation();
            setIsSelected((v) => !v);
          }}
          onKeyDown={(e) => {
            if (isEnterOrSpaceKeyEvent(e)) {
              setIsSelected((v) => !v);
            }
          }}
        >
          {leftIcon && <LeftIcon color={labelColor} iconName={leftIcon} size={size} />}
          {variant === "actionable" ? (
            <Icon iconName={Icons.MenuDots} size={size === "medium" ? 24 : 20} />
          ) : (
            <span
              id={`${dataTestId}-label`}
              className={classNames(styles.label, styles[size])}
              style={{ color: labelColor }}
            >
              {selectedItem?.label || label}
            </span>
          )}
          {variant === "selectable" && <ArrowIcon color={labelColor} isSelected={isSelected} />}
        </div>
      )}
      {isSelected && (
        <div
          id={`${dataTestId}-dropdown`}
          data-testid={`${dataTestId}-dropdown`}
          role="listbox"
          className={classNames(styles.dropdown, styles[direction], {
            [styles.stuckToRight]: asSplitButton,
          })}
          ref={dropdownRef}
        >
          {items.map((item) => (
            <NakedButton
              key={item.label}
              id={`${dataTestId}-item`}
              data-testid={`${dataTestId}-item`}
              role="option"
              aria-selected={selectedItem?.value === item.value}
              onClick={(event) => {
                event.stopPropagation();
                setIsSelected(false);
                onSelect(item.value);
                if (variant === "selectable") setSelectedItem(item);
              }}
              className={classNames(styles.item, styles[size], {
                [styles.selected]: selectedItem?.value === item.value,
                [styles.withLeftIcon]: item.leftIcon,
                [styles.withRightIcon]: item.rightIcon,
                [styles.withoutIcons]: !item.leftIcon && !item.rightIcon,
              })}
            >
              {item.leftIcon && <ItemIcon iconName={item.leftIcon} size={size} />}
              {item.label}
              {item.rightIcon && <ItemIcon iconName={item.rightIcon} size={size} />}
            </NakedButton>
          ))}
        </div>
      )}
    </div>
  );
};

const LeftIcon = ({ iconName, size, color }: { color?: Colors; iconName: Icons; size: Size }) => (
  <Icon
    className={classNames(styles.leftIcon, styles[size])}
    iconName={iconName}
    size={size === "medium" ? 20 : 16}
    color={color}
  />
);

const ArrowIcon = ({ isSelected, color }: { color?: Colors; isSelected: boolean }) => (
  <Icon
    className={classNames(styles.arrowIcon, {
      [styles.selected]: isSelected,
    })}
    iconName={Icons.ArrowFilledDown}
    size={16}
    color={color}
  />
);

const ItemIcon = ({ size, iconName }: { size: Size; iconName: Icons }) => (
  <Icon className={styles.itemIcon} iconName={iconName} size={size === "medium" ? 20 : 16} />
);
