import classNames from "classnames";
import React, { useEffect, useLayoutEffect, useRef, useState } from "react";
import { createPortal } from "react-dom";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { Button, ButtonProps } from "../Button/Button";
import { IconButton } from "../IconButton/IconButton";
import styles from "./BottomSheet.module.scss";

interface Cta extends ButtonProps {
  action: () => void;
  title: string;
}

interface MainCta extends Cta {
  stayOpenAfterAction?: boolean;
}

interface InnerSection {
  title?: string;
  button?: Cta;
  content: React.ReactNode;
}

export interface BottomSheetProps {
  dataTestId?: string;
  cta: MainCta[];
  title: string;
  onClose: () => void;
  hideCloseButton?: boolean;
  sections: InnerSection[];
  isOpen?: boolean;
  style?: React.CSSProperties;
  className?: string;
}

export const BottomSheet: React.FC<BottomSheetProps> = ({
  dataTestId,
  onClose,
  hideCloseButton,
  cta,
  title,
  sections,
  isOpen,
  style,
  className,
}) => {
  const [isClosing, setIsClosing] = useState(false);
  const [sheetHeight, setSheetHeight] = useState(0);
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  const contentRef = useRef<HTMLDivElement>(null);

  const handleClose = () => {
    setIsClosing(true);
    setTimeout(() => {
      setIsClosing(false);
      onClose();
    }, 400);
  };

  useEffect(() => {
    isOpen ? lockBodyScroll() : unlockBodyScroll();
    return () => unlockBodyScroll();
  }, [isOpen]);

  useLayoutEffect(() => {
    const contentHeight = contentRef.current?.clientHeight ?? 0;
    // header + footer + 2px buffer = 170px
    setSheetHeight(contentHeight + 170);
  }, [isOpen, sections]);

  if (!isOpen) return null;

  return (
    <>
      {isOpen &&
        createPortal(
          <div
            data-testid={dataTestId}
            className={classNames(styles.bottomSheet, { [styles.isClosing]: isClosing }, className)}
            style={style}
          >
            <div className={styles.sheet} style={{ height: sheetHeight }}>
              <div className={styles.layout}>
                <div className={styles.header}>
                  <div className={styles.title}>{title}</div>
                  {!hideCloseButton && (
                    <IconButton
                      dataTestId={dataTestId && `${dataTestId}-close-btn`}
                      className={styles.closeButton}
                      onClick={handleClose}
                      iconName={Icons.Cross}
                      iconColor={Colors.ContentPrimaryEmphasized}
                    />
                  )}
                </div>
                <div ref={scrollContainerRef} className={styles.contentWrapper}>
                  <div ref={contentRef}>
                    {sections.map((section, index) => (
                      <div key={index} className={styles.section}>
                        <div className={styles.sectionHeader}>
                          {section.title && (
                            <div className={styles.sectionTitle}>{section.title}</div>
                          )}
                          {section.button && (
                            <button
                              className={styles.sectionActionButton}
                              onClick={section.button.action}
                            >
                              {section.button.title}
                            </button>
                          )}
                        </div>
                        <div>{section.content}</div>
                      </div>
                    ))}
                  </div>
                </div>
                <div className={styles.footer}>
                  <div className={styles.actionWrapper}>
                    {cta.map(({ title, action, stayOpenAfterAction, ...buttonProps }, index) => (
                      <Button
                        {...buttonProps}
                        key={index}
                        dataTestId={dataTestId && `${dataTestId}-cta-btn-${index + 1}`}
                        className={styles.ctaButton}
                        onClick={() => {
                          action();
                          if (stayOpenAfterAction) {
                            return;
                          }
                          handleClose();
                        }}
                        style={{ width: "100%" }}
                      >
                        {title}
                      </Button>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          </div>,
          window.document.body
        )}
    </>
  );
};

const unlockBodyScroll = () => {
  document.body.style.removeProperty("overflow");
};

const lockBodyScroll = () => {
  document.body.style.overflow = "hidden";
};
