import classNames from "classnames";
import React, { useState } from "react";

import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { Badge } from "../Badge/Badge";
import { Icon } from "../Icon/Icon";
import { Tag } from "../Tag/Tag";
import styles from "./Thumbnails.module.scss";

const iconSizes = {
  large: 48,
  medium: 32,
  small: 24,
  extraSmall: 16,
};

export type ThumbnailSize = "large" | "medium" | "small" | "extraSmall";

interface ThumbnailsProps {
  dataTestId?: string;
  thumbnailUrls: string[];
  count?: number;
  tag?: string;
  size?: ThumbnailSize;
  thumbnailsLimit?: number;
}

export const Thumbnails: React.FC<ThumbnailsProps> = ({
  dataTestId,
  thumbnailUrls,
  count,
  tag,
  size = "small",
  thumbnailsLimit = 3,
}) => {
  const [containerGap, setContainerGap] = useState<number | null>(null);

  const limitedThumbnailUrls = thumbnailUrls.slice(0, thumbnailsLimit);

  return (
    <div data-testid={dataTestId} className={classNames(styles.container, styles[size])}>
      {limitedThumbnailUrls.length ? (
        <>
          {limitedThumbnailUrls.map((thumbnailUrl, idx, list) => (
            <div key={idx} className={classNames(styles.thumbnailContainer, styles[size])}>
              <img
                data-testid={dataTestId && `${dataTestId}-thumbnail-${idx + 1}`}
                alt={`Thumbnail ${idx + 1}`}
                className={classNames(styles.thumbnail, styles[size])}
                src={thumbnailUrl}
                onLoad={(event) => {
                  if (!count) return;

                  const imgEl = event.target as HTMLImageElement;
                  if (idx === list.length - 1 && imgEl.offsetParent) {
                    const thumbnailContainerWidth = imgEl.offsetParent.clientWidth;
                    const thumbnailWidth = imgEl.clientWidth;

                    setContainerGap(Math.ceil(thumbnailContainerWidth - thumbnailWidth));
                  }
                }}
              />
            </div>
          ))}
          {count && (
            <Badge
              style={{
                right: `${containerGap}px`,
                visibility: containerGap ? "visible" : "hidden",
              }}
              dataTestId={dataTestId && `${dataTestId}-count`}
              className={styles.count}
              number={count}
              bookBadge
            />
          )}
          {tag && size !== "extraSmall" && (
            <div className={styles.tagContainer}>
              <Tag dataTestId={dataTestId && `${dataTestId}-tag`}>{tag}</Tag>
            </div>
          )}
        </>
      ) : (
        <div className={classNames(styles.placeholder, styles[size])}>
          <Icon
            dataTestId={dataTestId && `${dataTestId}-placeholder`}
            color={Colors.ContentPrimaryMuted}
            iconName={Icons.Picture}
            size={iconSizes[size]}
          />
        </div>
      )}
    </div>
  );
};
