import { animated, easings, useSpring } from "@react-spring/web";
import {
  flexRender,
  getCoreRowModel,
  Row,
  RowData,
  RowSelectionState,
  SortingState,
  TableOptions,
  useReactTable,
} from "@tanstack/react-table";
import { useDrag } from "@use-gesture/react";
import classNames from "classnames";
import React, { useEffect, useRef, useState } from "react";

import { useResizeObserverMultiple } from "../../../hooks/useResizeObserverMultiple";
import { NakedButton } from "../../../shared/components/NakedButton/NakedButton";
import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import styles from "./Table.module.scss";

export const useSorting = (initialValue: SortingState = []) => {
  const [sorting, setSorting] = useState<SortingState>(initialValue);
  return { sorting, setSorting };
};

export const useRowSelection = (initialValue: RowSelectionState = {}) => {
  const [rowSelection, setRowSelection] = useState<RowSelectionState>(initialValue);
  return { rowSelection, setRowSelection };
};

declare module "@tanstack/react-table" {
  //eslint-disable-next-line
  interface ColumnMeta<TData extends RowData, TValue> {
    isAlignedRight: boolean; // for the 'meta' field
  }
}

interface SwipeActionConfig {
  type: string;
  iconName: Icons;
  label: string;
  onSwipeAction: (type: string, rowId: string) => void;
}

export interface TableProps<T> extends Omit<TableOptions<T>, "getCoreRowModel"> {
  dataTestId?: string;
  hasRowSelectionIcon?: boolean;
  hasTableHeaderDisplayed?: boolean;
  headerClassName?: string;
  headerStyle?: React.CSSProperties;
  rowSwipeActionEnabled?: (row: Row<T>) => SwipeActionConfig | null;
}
export const Table = <T extends {}>({
  dataTestId,
  hasRowSelectionIcon = true,
  hasTableHeaderDisplayed = true,
  headerClassName,
  headerStyle,
  rowSwipeActionEnabled,
  ...restProps
}: TableProps<T>) => {
  const table = useReactTable<T>({
    ...restProps,
    getCoreRowModel: getCoreRowModel(),
    defaultColumn: {
      minSize: 0,
      size: Number.MAX_SAFE_INTEGER,
      maxSize: Number.MAX_SAFE_INTEGER,
      // this enables proper column sizing with table-layout: fixed
      // see https://github.com/TanStack/table/discussions/3192#discussioncomment-6458134
    },
  });

  const hasSwipeActions = table.getRowModel().rows.some((row) => rowSwipeActionEnabled?.(row));

  const tableBodyRef = useRef<HTMLTableSectionElement>(null);

  const observeRows = useResizeObserverMultiple((entry) => {
    const row = entry.target as HTMLTableRowElement;
    const rowId = row.dataset.id;
    const height = entry.contentRect.height;

    if (!rowId || rowElementHeights[rowId] === height) return;

    setRowElementHeights((prevState) => ({ ...prevState, ...{ [rowId]: height } }));
  });

  const rowModel = table.getRowModel();

  useEffect(() => {
    if (tableBodyRef.current && hasSwipeActions) {
      const rows = tableBodyRef.current.querySelectorAll("tr");
      observeRows(rows);
    }
  }, [rowModel, observeRows, hasSwipeActions]);

  const [rowElementHeights, setRowElementHeights] = useState<Record<string, number>>({});
  const [armedActionRows, setArmedActionRows] = useState<Set<string>>(new Set());

  return (
    <div className={styles.tablesWrapper}>
      {hasSwipeActions && (
        <table
          className={classNames(styles.swipeActionsTable, {
            [styles.withHeader]: hasTableHeaderDisplayed,
          })}
        >
          <tbody>
            {table.getRowModel().rows.map((row) => {
              const rowHeight: number | undefined = rowElementHeights[row.id];
              const swipeActionConfig = rowSwipeActionEnabled?.(row);
              return (
                <tr
                  key={row.id}
                  style={{
                    height: rowHeight ?? 100,
                    backgroundColor:
                      swipeActionConfig && rowHeight
                        ? Colors.SurfaceAccentPeriwinkle
                        : Colors.SurfaceNeutralMuted,
                    visibility: swipeActionConfig ? "visible" : "hidden",
                  }}
                >
                  {swipeActionConfig && (
                    <ActionCell
                      iconName={swipeActionConfig.iconName}
                      label={swipeActionConfig.label}
                      armed={armedActionRows.has(row.id)}
                    />
                  )}
                </tr>
              );
            })}
          </tbody>
        </table>
      )}
      <table data-testid={dataTestId} className={styles.table}>
        {hasTableHeaderDisplayed && (
          <thead className={classNames(headerClassName)} style={headerStyle}>
            {table.getHeaderGroups().map((headerGroup) => (
              <tr key={headerGroup.id}>
                {headerGroup.headers.map((header) => {
                  const isSorted = header.column.getIsSorted();
                  const canBeSorted = header.column.getCanSort();
                  const sortIcon = isSorted
                    ? { asc: Icons.ArrowFilledUp, desc: Icons.ArrowFilledDown }[isSorted]
                    : Icons.SortFilled;
                  const headerContent = flexRender(
                    header.column.columnDef.header,
                    header.getContext()
                  );

                  return (
                    <th
                      key={header.id}
                      style={{
                        width:
                          header.getSize() === Number.MAX_SAFE_INTEGER ? "auto" : header.getSize(),
                      }}
                    >
                      <NakedButton
                        className={classNames(styles.header, {
                          [styles.isSorted]: isSorted,
                          [styles.canBeSorted]: canBeSorted,
                          [styles.alignRight]: header.column.columnDef.meta?.isAlignedRight,
                          [styles.noMarginBottom]: !headerContent,
                        })}
                        onClick={() => {
                          canBeSorted && header.column.toggleSorting();
                        }}
                        disabled={!canBeSorted}
                      >
                        {headerContent}
                        {canBeSorted && (
                          <Icon
                            color={isSorted ? undefined : Colors.ContentPrimaryMuted}
                            iconName={sortIcon}
                            size={16}
                          />
                        )}
                      </NakedButton>
                    </th>
                  );
                })}
              </tr>
            ))}
          </thead>
        )}
        <tbody ref={tableBodyRef}>
          {table.getRowModel().rows.map((row) => {
            const { getIsSelected, toggleSelected } = table.getRow(row.id);
            const isSelected = getIsSelected();
            const swipeActionConfig = rowSwipeActionEnabled?.(row);

            return (
              <SwipeableTr
                data-id={row.id}
                key={row.id}
                swipeEnabled={!!swipeActionConfig}
                onClick={() => {
                  if (!isSelected && !document.body.classList.contains("dragging"))
                    toggleSelected();
                }}
                className={classNames(styles.row, {
                  [styles.isRowSelected]: isSelected,
                })}
                onSwipeArm={() =>
                  setArmedActionRows((prevState) => {
                    const newSet = new Set(prevState);
                    newSet.add(row.id);
                    return newSet;
                  })
                }
                onSwipeDisarm={() =>
                  setArmedActionRows((prevState) => {
                    const newSet = new Set(prevState);
                    newSet.delete(row.id);
                    return newSet;
                  })
                }
                onSwipeCommit={() => {
                  swipeActionConfig?.onSwipeAction(swipeActionConfig.type, row.id);
                }}
              >
                {row.getVisibleCells().map((cell, idx, list) => (
                  <td
                    key={cell.id}
                    className={classNames(styles.cell, {
                      [styles.alignRight]: cell.column.columnDef.meta?.isAlignedRight,
                    })}
                    style={{
                      width:
                        cell.column.getSize() === Number.MAX_SAFE_INTEGER
                          ? "auto"
                          : cell.column.getSize(),
                    }}
                  >
                    {flexRender(cell.column.columnDef.cell, cell.getContext())}
                    {idx === list.length - 1 && hasRowSelectionIcon && (
                      <RowSelectionIcon isRowSelected={isSelected} />
                    )}
                  </td>
                ))}
              </SwipeableTr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};

type SwipeableTrProps = React.ComponentPropsWithoutRef<"tr"> & {
  swipeEnabled?: boolean;
  onSwipeArm?: () => void;
  onSwipeDisarm?: () => void;
  onSwipeCommit?: () => void;
};

const ARM_TRESHOLD = -80;

const SwipeableTr = React.forwardRef<HTMLTableRowElement, SwipeableTrProps>(
  ({ children, swipeEnabled, onSwipeArm, onSwipeDisarm, onSwipeCommit, ...rest }, ref) => {
    const isArmed = useRef(false);
    const [{ x }, api] = useSpring(() => ({
      x: 0,
      config: {
        tension: 170,
        friction: 26,
        clamp: true,
      },
    }));

    useEffect(() => {
      api.start({ from: { x: 0 }, to: [{ x: -75 }, { x: 0 }], delay: 1000 });
    }, [api]);

    // Set the drag hook and define component movement based on gesture data
    const bind = useDrag(
      ({ down, dragging, movement: [mx], last }) => {
        if (mx < ARM_TRESHOLD && !isArmed.current) {
          isArmed.current = true;
          onSwipeArm?.();
        }

        if (last && isArmed.current) {
          onSwipeCommit?.();
        }

        if ((mx > ARM_TRESHOLD || last) && isArmed.current) {
          isArmed.current = false;
          onSwipeDisarm?.();
        }

        if (dragging) {
          document.body.classList.add("dragging");
        } else {
          // we defer the removal of the dragging indicator to make click events distinguishable from dragging
          // dragging != selecting
          setTimeout(() => document.body.classList.remove("dragging"), 300);
        }

        api.start({ x: down && mx < 0 ? mx : 0, immediate: down });
      },
      { axis: "x" }
    );

    return swipeEnabled ? (
      <animated.tr ref={ref} {...rest} {...bind()} style={{ x, touchAction: "pan-y" }}>
        {children}
      </animated.tr>
    ) : (
      <tr ref={ref} {...rest}>
        {children}
      </tr>
    );
  }
);

interface ActionCellProps {
  armed?: boolean;
  iconName: Icons;
  label: string;
}

const ActionCell: React.FC<ActionCellProps> = ({ iconName, label, armed }) => {
  const [springs, api] = useSpring(() => ({
    scale: 1,
    config: { easing: easings.easeOutExpo, duration: 400 },
  }));

  useEffect(() => {
    api.start({
      from: {
        scale: armed ? 1 : 1.5,
      },
      to: {
        scale: armed ? 1.5 : 1,
      },
    });
  }, [api, armed]);

  return (
    <td className={styles.cell}>
      <div className={styles.swipeAction}>
        <animated.div style={{ ...springs }}>
          <Icon iconName={iconName} size={16} color={Colors.ContentPrimaryContrast} />
        </animated.div>
        <span>{label}</span>
      </div>
    </td>
  );
};

const RowSelectionIcon = ({ isRowSelected }: { isRowSelected: boolean }) =>
  // TODO: Create an SVG animation for the icons below: https://app.clickup.com/t/8587959/LU-8918
  isRowSelected ? (
    <Icon iconName={Icons.VerticalPillar} className={styles.verticalPillarIcon} size={18} />
  ) : (
    <Icon iconName={Icons.ForwardArrow} className={styles.arrowIcon} size={18} />
  );
