import classNames from "classnames";
import React, { useEffect, useRef, useState } from "react";
import { useDebouncedCallback } from "use-debounce";

import { useCopy } from "../../../extensions";
import { Colors } from "../../constants";
import { Icons } from "../../icon-types";
import { Icon } from "../Icon/Icon";
import styles from "./SearchInput.module.scss";

export interface SearchInputProps {
  dataTestId?: string;
  placeholder?: string;
  value?: string;
  onChange: (value: string) => void;
  delay?: number;
  alwaysOpen?: boolean;
}

export const SearchInput: React.FC<SearchInputProps> = ({
  dataTestId,
  placeholder,
  value,
  onChange,
  delay,
  alwaysOpen,
}) => {
  const [internalValue, setInternalValue] = useState(value);
  const inputRef = useRef<HTMLInputElement>(null);
  const { _textPlaceholder, _textClearAll } = useCopy("SearchInput");

  useEffect(() => {
    setInternalValue(value);
  }, [value]);

  const debouncedOnChange = useDebouncedCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    onChange(event.target.value);
  }, delay ?? 800);

  return (
    <div
      className={classNames(styles.searchInput, { [styles.alwaysOpen]: alwaysOpen })}
      data-testid={dataTestId}
    >
      <div
        role="presentation"
        className={styles.background}
        onMouseDown={(event) => {
          event.preventDefault();
        }}
        onClick={() => {
          inputRef.current?.focus();
        }}
        aria-label="Search button"
      />
      <Icon className={styles.icon} iconName={Icons.Search} color={Colors.ContentPrimarySubtle} />
      {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events */}
      <span
        role="button"
        tabIndex={-1}
        className={styles.reset}
        onClick={() => {
          if (inputRef.current) {
            inputRef.current.focus();
            inputRef.current.value = "";
            setInternalValue("");
            onChange("");
          }
        }}
      >
        {_textClearAll}
      </span>
      <input
        aria-label={_textPlaceholder}
        value={internalValue ?? ""}
        className={styles.input}
        ref={inputRef}
        placeholder={placeholder ?? _textPlaceholder}
        onChange={(event) => {
          setInternalValue(event.target.value);
          debouncedOnChange(event);
        }}
      />
    </div>
  );
};
